/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkd-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;

    .touch & {
        cursor: pointer;
    }

    .mkd-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;

        a {
            color: $widget-text-color;

            &:hover {
                color: $first-main-color;
            }
        }

        .mkd-cart-info {
            font-size: 13px;
            font-weight: 500;
            padding-left: 5px;
        }
    }

    .mkd-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .mkd-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            font-size: 24px;
            line-height: 1em;

            &:before {
                display: block;
                line-height: inherit;
            }
        }

        .mkd-cart-number {
            position: absolute;
            display: inline-block;
            width: 15px;
            line-height: 15px;
            float: right;
            height: 15px;
            top: -3px;
            font-size: 9px;
            border-radius: 50%;
            background-color: $first-main-color;
            color: #fff;
            font-weight: 400;
            font-family: $default-text-font;
            text-align: center;
            right: -6px;
        }
    }

    &:hover {

        .mkd-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.mkd-dark-header .mkd-page-header > div:not(.mkd-sticky-header):not(.fixed) .mkd-shopping-cart-holder .mkd-header-cart {
    color: $header-dark-color;

    .mkd-cart-number {
        background-color: $header-dark-color;
        color: $header-light-color;
    }

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkd-logo-area-dark-header .mkd-page-header > div:not(.mkd-sticky-header):not(.fixed) .mkd-vertical-align-containers .mkd-shopping-cart-holder .mkd-header-cart {
    color: $header-dark-color;

    .mkd-cart-number {
        background-color: $header-dark-color;
        color: $header-light-color;
    }

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkd-light-header .mkd-page-header > div:not(.mkd-sticky-header):not(.fixed) .mkd-shopping-cart-holder .mkd-header-cart {
    color: $header-light-color;

    .mkd-cart-number {
        background-color: $header-light-color;
        color: $first-main-color;
    }

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkd-logo-area-light-header .mkd-page-header > div:not(.mkd-sticky-header):not(.fixed) .mkd-vertical-align-containers .mkd-shopping-cart-holder .mkd-header-cart {
    color: $header-light-color;

    .mkd-cart-number {
        background-color: $header-light-color;
        color: $first-main-color;
    }

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkd-shopping-cart-dropdown {
    position: absolute;
    visibility: hidden;
    box-sizing: border-box;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 280px;
    background-color: #fff;
    box-shadow: 0px 1px 18.8px 1.2px rgba(87, 98, 101, 0.06);
    text-align: left;
    z-index: 1000;
    opacity: 0;

    @include mkdTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 25px;
        padding: 0;

        li {
            @include mkdTableLayout();
            padding: 20px 0 0;
            box-sizing: border-box;

            &.mkd-empty-cart {
                color: initial;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkd-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 53px;
        vertical-align: middle;

        img {
            display: block;
        }
    }

    .mkd-item-info-holder {
        @include mkdTableCellLayout();
        vertical-align: middle;
        padding-left: 20px;

        .mkd-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 12px;
	        line-height: 1;
            color: $first-main-color;

            &:hover {
                color: #000;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkd-quantity {
            display: inline-block;
            margin: 3px 0 0;
            font-size: 11px;
            font-weight: 400;
            color: $first-main-color;
        }

        .amount {
            display: inline-block;
            margin: 3px 0 0;
            font-size: 11px;
            font-weight: 400;
            color: $first-main-color;
        }
    }

    .mkd-cart-bottom {
        padding: 21px 0 0;

        .mkd-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 16px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }

            .mkd-total-amount {
                text-align: right;
	            font-size: 16px;
                font-weight: 500;
            }

            .mkd-total {
                font-size: 18px;
            }
        }

        .mkd-view-cart {
            @include mkdRelativeHolderLayout();
            height: 44px;
            width: 49%;
            line-height: 44px;
            font-size: 12px;
            font-weight: 500;
            color: #fff;
            background-color: $first-main-color;
            text-align: center;
            box-sizing: border-box;
            @include mkdTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: #3e71fa;
            }
        }

        .mkd-checkout {
            @include mkdRelativeHolderLayout();
            height: 44px;
            width: 49%;
            line-height: 44px;
            font-size: 12px;
            font-weight: 500;
            color: #272626;
            background-color: #e0e1e9;
            text-align: center;
            box-sizing: border-box;
            @include mkdTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: $first-main-color;
            }
        }
    }
}

.mkd-vertical-menu-area {

    .mkd-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */