.mkd-product-categories-widget {
    .mkd-product-categories-holder {
        margin: 0 -5px;
    }

    .mkd-product-category {
        width: 33.3333%;
        padding: 5px;
        box-sizing: border-box;
        float: left;
    }

    .mkd-product-category-inner {
        position: relative;
        height: 100%;
        background-color: #faf8f8;

        img {
            display: block;
        }

        .mkd-cat-link {
            @include mkdAbsoluteHolderLayout();
        }

        &:hover {
            .mkd-product-cat-name-holder {
                opacity: 1;
            }
        }
    }

    .mkd-product-cat-name-holder-inner {
        display: table;
        height: 100%;
        width: 100%;
    }

    .mkd-product-cat-name-holder {
        @include mkdAbsoluteHolderLayout();
        opacity: 0;
        background-color: #faf8f8;

        @include mkdTransition(opacity 0.3s ease);
    }

    .mkd-product-category-name {
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        color: $first-main-color;
        font-size: 12px;
        font-weight: 500;
    }
}