/* ==========================================================================
   WooCommerce Track Order page styles - begin
   ========================================================================== */

.mkd-woocommerce-page.mkd-woocommerce-order-tracking {
    
    .woocommerce {
        margin: 0 0 45px;
        
        > .track_order {
            display: block;
            margin: 0 0 60px;
            
            > p:not(.form-row) {
                display: block;
                margin: 0 0 7px;
            }
            
            .form-row-first,
            .form-row-last {
                @include mkdRelativeHolderLayout();
                margin: 0;
                
                label {
                    display: block;
                    margin: 30px 0 13px;
                    color: #343443;
                    font-size: 12px;
                    line-height: 11px;
                    font-weight: 500;
                    text-transform: capitalize;

                    &:after {
                        content: '*';
                    }
                }

                .input-text {
                    border: 1px solid #daddec;
                    height: 50px;
                    margin-bottom: 2px;

                    &:focus {
                        border: 1px solid #41c4fd;
                    }

                    @include mkdPlaceholder {
                        font-size: 0;
                    }
                }
            }
            
            .button {
                margin: 16px 0 0;
                @include mkdButtonRoundedShape();
                @include mkdButtonLargeParams();
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Track Order page styles - end
   ========================================================================== */