/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .mkd-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.mkd-woo-search-widget-button),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include mkdButtonDefaultStyle();
        @include mkdButtonLargeParams();
        @include mkdButtonSolidColor();
        padding: 13px 31px;
        cursor: pointer;
        z-index: 3;

        &:hover {
            background-color: $solid-hover-button-color;
            @include mkdTransition(background-color .2s ease-in);
        }
    }
    
    a.button.added {
        display: none;
    }
}
.woocommerce-page .mkd-content {
    input[type="submit"] {
        border-radius: 2em;
        line-height: 14px;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */