/* Woocommerce Product Categories shortcode style - begin
========================================================================== */

div.woocommerce {

    > .products {


        > .product-category {

            padding: 0;
            margin: 0 0 20px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                text-align: center;
                padding: 0 36px;
                background-color: #fafafa;

                .woocommerce-loop-category__title {
                    font-size: 16px;
                    font-weight: 500;
                    margin: 0;
                    padding-bottom: 50px;
                    @include mkdTransition(color .2s ease-in);
                    mark {
                        display: none;
                    }
                    &:hover {
                        color: $first-main-color;

                    }
                }

                &:after {
                    display: none;
                }

                &:hover {

                    &:after {
                        opacity: 1;                        
                    }

                    h3 {
                        top: 50%;
                        opacity: 1;
                    }
                }
            }

            img {
                display: block;
            }

            h3 {
                position: absolute;
                top: 60%;
                left: 0;
                display: block;
                width: 100%;
                padding: 0 10%;
                margin: 0;
                color: #fff;
                text-align: center;
                z-index: 2;
                opacity: 0;
                -webkit-filter: blur(0);
                box-sizing: border-box;
                @include mkdTransform(translateY(-50%));
                @include mkdTransition(opacity .2s ease, top .2s ease);

                .count {
                    font-size: 0.9em;
                    color: #fff;
                    background-color: transparent;
                }
            }
        }
    }
}
/* Woocommerce Product Categories shortcode style - end
========================================================================== */