/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_price_filter,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_product_search,
    &.widget_product_tag_cloud,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        margin: 0 0 40px;
    }
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include mkdRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }


    &.widget_layered_nav,
    &.widget_price_filter,
    &.widget_layered_nav_filters,
    &.widget_product_categories {
        ul {
            li {
                overflow: hidden;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;

                    &:before,
                    &:after {
                        content: '';
                        position: absolute;
                        width: 100%;
                        left: 0;
                        bottom: -2px;
                        border-bottom: 2px solid $first-main-color;
                        opacity: 0;
                        transform-origin: 100% 50%;
                    }
                    &:before {
                        @include mkdTransform(scaleX(0));
                        transform-origin: 0 50%;
                        -webkit-transition: transform 0s .2s;
                        -moz-transition: transform 0s .2s;
                        transition: transform 0s .2s;
                    }

                    &:after {
                        @include mkdTransform(scaleX(0));
                        opacity: 0;
                        -webkit-transition: transform .2s,opacity 0s .2s;
                        -moz-transition: transform .2s,opacity 0s .2s;
                        transition: transform .2s,opacity 0s .2s;
                    }

                    &:hover {
                        color: $first-main-color;

                        &:before {
                            @include mkdTransform(scaleX(1));
                            opacity: 1;
                            @include mkdTransition(all .2s ease-in-out);
                        }

                        &:after {
                            @include mkdTransform(scaleX(1));
                            opacity: 1;
                            @include mkdTransition(all 0s .2s);
                        }
                    }
                }
            }
        }
    }


    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include mkdRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include mkdRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .mkd-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .mkd-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #b3bbe9;
                margin-top: 13px;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -4px;
                    height: 12px;
                    width: 12px;
                    border-radius: 50%;
                    background-color: $first-main-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        @include mkdTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $first-main-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            position: relative;
            margin: 23px 0 0;
            
            .button {
                margin-top: 37px;
                border-radius: 2em;
                padding: 6px 29px;
                font-size: 13px;
                line-height: 1.5em;
            }
            
            .price_label {
                position: absolute;
                top: -5px;
                left: 0;
                font-size: 13px;
                font-weight: 400;
                color: #494949;
            }
        }
    }
    
    &.widget_product_categories {
        
        ul {
            
            li {
                margin: 2px 0;
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products{
    
        .mkd-widget-title {
            margin: 0 0 29px;
        }
        
        ul {
            
            li {
                min-height: 110px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 102px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 102px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: inherit;
                }
                
                .amount {
                    color: $default-heading-color;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }

    &.widget_top_rated_products {

        .mkd-widget-title {
            margin: 0;
        }

        ul {

            li {

                .star-rating {
                    display: none;
                }

                ins,
                del {
                    display: inline-block;
                }

                ins {
                    color: #ff4b4b;
                    text-decoration: none;
                    vertical-align: middle;
                    display: inline-block;
                    line-height: 1;
                    font-size: 16px;
                    font-weight: 500;
				}

                del {
                    margin: 0 8px 0 0;
                    font-size: 14px;
                    font-weight: 500;
                    color: #d2d2d2;
                    vertical-align: middle;
                    display: inline-block;
                }

                a img:hover {
                    + .product-title {
                        color: $first-main-color;
                    }
                }

                > span {
                    display: block;
                    font-size: 16px;
                    font-weight: 500;
                    color: #000;
                }

                .product-title {
                    color: #000;
                    font-size: 12px;
                    font-weight: 500;
                    @include mkdTransition(color .25s ease-in-out);

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin-right: 7px;
                font-family: inherit;
                background-color: transparent;
                font-size: 14px !important;
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include mkdRelativeHolderLayout();
            clear: both;
            
            > div {
                @include mkdRelativeHolderLayout();
                border: 1px solid $default-border-color;
                box-sizing: border-box;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 44px;
                line-height: 42px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include mkdTransition(color .2s ease-out);
            }
            
            input {
                width: calc(100% - 40px);
                padding: 0 10px 0 20px;
                font-family: inherit;
                font-size: 13px;
    
                @include mkdPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 40px;
                padding: 0 13px 0 4px;
                font-size: 14px;
                cursor: pointer;
                
                &:hover {
                    color: $default-heading-color;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    &.product_list_widget {
        .amount {
            font-size: 18px;
            font-weight: 500;
        }
        ins span.amount {
            float: left;
            color: #ff4b4b;
        }
        del {
            margin: 0 !important;
            color: #d2d2d2;
            span.amount {
                margin-left: 5px;
                color: #d2d2d2;
                font-size: 14px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */