/* ==========================================================================
   WooCommerce Variation styles - begin
   ========================================================================== */

.mkd-woocommerce-page .mkd-content {
    
    .variations {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        border: none;
        padding: 0;
        
        tr {
            display: block;
            padding: 0 0 20px;
            border: 0;
            
            &:last-child {
                margin: 0;
            }
        }
        
        td {
            padding: 0;
            text-align: initial;
	        
	        &.label {
                display: block;
                font-size: 12px;
                font-weight: 500;
                color: #000;
                text-transform: capitalize;
                margin-bottom: 8px;
	        }
	        
	        &.value {
		        white-space: nowrap;

	        }
        }
        
        .reset_variations {
            margin: 0 0 0 10px;
            color: #908989;
            font-size: 12px;
            font-weight: 400;
            display: none;
        }
    }
    
    .single_variation_wrap {
        
        .single_variation {

            .woocommerce-variation-price {
                margin: 0;
                .price {
                    padding: 0;
                }
            }
        }
    }
    
    table.group_table {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0 0 28px;
        border: none;
        padding: 0;

        
        tbody {
            display: block;
        }

        
        tr {
            display: table;
            border-bottom: none;
            
            &:last-child {
                margin: 0;
                border-bottom: 1px solid $default-border-color;
            }
        }
        
        td {
            padding: 0;
            text-align: right;
            display: inline-block;

            
            &.price {
                display: table-cell;
                position: relative;
                width: 200px;

                .amount {
                    padding-right: 15px;
                    position: absolute;
                    left: 0;
                    top: 50%;
                    @include mkdTransform(translateY(-50%));
                }

                del {
                    .amount {
                        left: 30%;
                        font-size: 16px;
                        text-decoration: line-through;
                    }
                }

                p.stock.in-stock {
                    position: absolute;
                    right: 0;
                    top: 50%;
                    @include mkdTransform(translateY(-50%));
                    padding-right: 20px;
                    padding-left: 30px;
                    margin: 0;
                    span {
                        display: none;
                    }
                }
            }

            &.label {
                vertical-align: middle;
                margin: 12px 0 12px 6px;
                line-height: 1.43em;
                max-width: 200px;
                text-align: left;

            }

        }
        
        .mkd-quantity-buttons {
            margin: 0;
            border: 0;
            .mkd-quantity-label {
                display: none;
            }
        }
        
        a {
            color: #444;
            font-weight: 400;
            
            &:hover {
                color: $first-main-color;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Variation styles - end
   ========================================================================== */