/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .mkd-onsale,
    .mkd-out-of-stock,
    .mkd-new-product {
        position: absolute;
        display: block;
        top: 0;
        width: auto;
        height: 24px;
        color: #fff;
        font-size: 8px;
        line-height: 24px;
        letter-spacing: 1px;
        padding: 0 16px;
        margin: 0;
        text-align: center;
        z-index: 100;
        @include mkdTransform(translateZ(0));
        box-sizing: border-box;
    }
    
    .mkd-onsale {
        left: 0;
        background-color: rgba($default-heading-color, .5);
    }
    
    .mkd-out-of-stock {
        right: 0;
        background-color: rgba($default-heading-color, .5);
    }
}

.mkd-woo-single-page {
    
    .mkd-single-product-content {
        
        .mkd-onsale {
            left: 10px;
        }
        
        .mkd-out-of-stock {
            right: 10px;
        }
    }
    
    &.mkd-woo-single-thumb-on-left-side {
        
        .mkd-single-product-content {
            
            .mkd-onsale {
                left: 120px;
            }
            
            .mkd-out-of-stock {
                right: 0;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */