/* ==========================================================================
   YITH WishList styles - begin
   ========================================================================== */

#yith-wcwl-popup-message {
    position: absolute; //absolute centering over added item
    margin:0 !important; //absolute centering over added item
    -webkit-transform-origin:0 0;
    transform-origin:0 0;
    @include mkdTransform(translate(-50%,-50%));
    border: 1px solid #ebebeb;
    border-radius: 0;
    padding: 0 21px;
    line-height: 35px;

    #yith-wcwl-message {
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 0.1em;

        &:before {
            position: relative;
            font-family: 'ElegantIcons';
            content: "\4e";
            top: 2px;
            right: 7px;
        }
    }
 
    //vanishing effect
    &.mkd-wishlist-vanish-out {
      @include mkdAnimation(mkdWishlistvanishOut 1s both);
    }

    @keyframes mkdWishlistvanishOut {
      0% {
        opacity: 1;
        @include mkdTransform(scale(1,1) translate(-50%,-50%));
        -webkit-filter: blur(0px);
        filter: blur(0px);
      }

      100% {
        opacity: 0;
        @include mkdTransform(scale(2,2) translate(-50%,-50%));
        -webkit-filter: blur(10px);
        filter: blur(10px);
      }
    }
}

.mkd-yith-wcqv-holder {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.yith-wcwl-add-to-wishlist {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    background-color: transparent;
    text-align: center;
}

.yith-wcwl-add-button,
.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        position: relative;
        display: inline-block;
        vertical-align: top;
        font-size: 0;
        line-height: inherit;
        color: $additional-text-color;

        &:after {
            position: relative;
            display: inline-block;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            @include mkdTransition(opacity .2s);
            content: "\e004";
            font-family: dripicons-v2!important;
            font-size: 16px;
            padding: 0 10px;
            vertical-align: baseline;
        }

        // content: "\4e";

        &:not(.mkd-adding-to-wishlist):hover {
            &:after {
                opacity: .8;
            }
        }

        &.mkd-adding-to-wishlist {
            @include keyframes(mkdWishlistLoading) {
                0%, 100% {
                    opacity:1;
                }

                50% {
                    opacity: 0.2;
                }
            }

            &:after {
                @include mkdAnimation(mkdWishlistLoading 1s ease infinite);
            }
        }

        &:hover {
            color: $first-main-color !important;
        }
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        &:after {
            content: "\e004";
            font-family: dripicons-v2!important;
        }
    }
}

.yith-wcwl-add-button {
    .ajax-loading {
        display: none !important;
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    span {
        display: none;
    }

    a {

        &:after {
            color: $first-main-color;
        }
    }
}

.woocommerce-wishlist {

    .wishlist-title{
        display: none;
    }

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 15px 30px;
        margin: 50px 0 30px;
        line-height: 48px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }
    }

    table.wishlist_table {
        font-size: inherit;
        margin: 50px 0 100px;

        thead {
            display: none;
        }

        tbody {

            tr {
                border: 0;
                border-bottom: 1px solid $default-border-color;

                td {
                    padding: 22px 0;
                    vertical-align: middle;
                    text-align: center;

                    &.product-remove {

                        a {
                            color: $default-text-color;
                            font-size: 0;

                            &:after {
                                content: "\39";
                                display: inline-block;
                                vertical-align: middle;
                                speak: none;
                                color: $default-heading-color;
                                font-family: "dripicons-v2" !important;
                                font-size: 22px;
                                font-style: normal;
                                font-weight: normal;
                                font-variant: normal;
                                text-transform: none;
                                text-rendering: auto;
                                line-height: 1;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }

                            &:hover {
                                color: $default-heading-color;
                            }
                        }
                    }

                    &.product-thumbnail {
                        padding-left: 8px;
                        padding-right: 8px;
                        width: 92px;

                        a, img {
                            display: block;
                        }
                    }

                    &.product-name {
                        text-align: left;
                        padding-left: 20px;

                        a {
                            font-size: 16px;
                            font-weight: 500;
                            text-transform: capitalize;
                        }

                        .yith-wcqv-button {
                            height: 38px;
                            line-height: 38px;
                            padding: 0 28px;
                            margin: 0;
                        }
                    }

                    &.product-price{
                        font-family: $default-text-font;
                        font-size: 16px;
                        font-weight: 500;
                        color: $default-heading-color;

                        ins{
                            text-decoration: none;
                            color: #ff4b4b;
                        }

                        del {
                            color: #d2d2d2;
                        }
                    }

                    &.product-stock-status span{
                        color: $value-deal-color !important;
                        font-size: 13px;
                        font-weight: 400;
                    }

                    &.product-add-to-cart {

                        a {
                            display: inline-block !important;
                            color: #fff;
                            background-color: #5677fc;
                            border: 2px solid transparent;
                            font-family: inherit;
                            width: auto;
                            outline: 0;
                            line-height: 20px;
                            font-weight: 500;
                            text-transform: capitalize;
                            box-sizing: border-box;
                            margin: 0;
                            @include mkdTransition(color .2s ease-in-out,background-color .2s ease-in-out,border-color .2s ease-in-out);
                            padding: 8px 31px;
                            font-size: 12px;
                            cursor: pointer;
                            border-radius: 2em;

                            &:hover{
                                border-color: #3e71fa !important;
                                background-color: #3e71fa !important;
                            }
                        }
                    }
                }
            }
        }

        .yith-wcwl-share{
            margin: 0;
        }
    }
}

.mkd-single-product-summary {
    .yith-wcwl-add-to-wishlist {
        width: auto;
        background-color: transparent;
        float: left;

        .yith-wcwl-add-button,
        .yith-wcwl-wishlistaddedbrowse,
        .yith-wcwl-wishlistexistsbrowse {
            a {
                font-size: 12px;
                color: $default-heading-color;
                text-transform: capitalize;
                line-height: 22px;
                font-weight: 500;

                &:after {
                    content: "\e004";
                    font-size: 16px;
                    float: left;
                    padding-right: 10px;
                    line-height: 19px;
                    color: $default-heading-color;
                }
                &:hover {
                    &:after {
                        color: $first-main-color;
                        @include mkdTransition(color .2s ease-in);

                    }
                }
            }
        }

        .yith-wcwl-add-button {
            display: inline-block;
            a {
                &:after {
                    content: "\e004";
                    padding-left: 0;
                }


            }
        }
    }
}

.mkd-wishlist-widget-holder{
    display: inline-block;
    vertical-align: middle;

    a {
        color: $widget-text-color;
        font-size: 24px;
        vertical-align: middle;

        .mkd-wishlist-items-number {
            color: $default-text-color;
            font-family: $default-text-font;
            font-size: 12px;
        }

        &:hover {
            color: $first-main-color;
        }
    }
}

.mkd-light-header {
    .mkd-wishlist-widget-holder{
        a {
            color: $header-light-color;
            .mkd-wishlist-items-number {
                color: $header-light-color;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}

.mkd-logo-area-light-header {
    .mkd-vertical-align-containers {
        .mkd-wishlist-widget-holder {
            a {
                color: $header-light-color;
                .mkd-wishlist-items-number {
                    color: $header-light-color;
                }

                &:hover {
                    opacity: 0.8;
                }
            }
        }
    }
}

.mkd-dark-header {
    .mkd-wishlist-widget-holder{
        a {
            color: $header-dark-color;
            .mkd-wishlist-items-number {
                color: $header-dark-color;

            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}

.mkd-logo-area-dark-header {
    .mkd-vertical-align-containers {
        .mkd-wishlist-widget-holder {
            a {
                color: $header-dark-color;
                .mkd-wishlist-items-number {
                    color: $header-dark-color;

                }

                &:hover {
                    opacity: 0.8;
                }
            }
        }
    }
}

/* ==========================================================================
   YITH WishList styles - end
   ========================================================================== */