/* ==========================================================================
   Product Info shortcode styles - begin
   ========================================================================== */

.mkd-product-info {
    position: relative;
    display: inline-block;
    vertical-align: middle;

    > div {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        p {
            margin: 0;
        }

        a {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 9px 0 0;

            &:last-child {
                margin: 0;
            }
        }
    }

    .mkd-pi-title,
    .mkd-pi-image,
    .mkd-pi-rating {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
    }

    .mkd-pi-image {

        img {
            display: block;
        }
    }

    .mkd-pi-category {

        h1, h2, h3, h4, h5, h6 {
            margin: 0;

            a {
                vertical-align: top;
            }
        }
    }

    .mkd-pi-rating {
        font-family: "ElegantIcons";
        font-size: 12px;
        line-height: 1;
        letter-spacing: 2px;
        color: $default-text-color;
        overflow: hidden;
        -webkit-font-smoothing: antialiased;

        &:before {
            content: "\e031\e031\e031\e031\e031";
        }

        span {
            position: absolute;
            top: 0;
            left: 0;
            overflow: hidden;

            &:before {
                content: "\e033\e033\e033\e033\e033";
            }
        }
    }

    .mkd-pi-add-to-cart {

        .mkd-btn.mkd-btn-solid {

            &.mkd-white-skin {
                color: $default-heading-color;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff;
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }

            &.mkd-dark-skin {
                color: #fff;
                background-color: $default-heading-color;
                border-color: $default-heading-color;

                &:hover {
                    color: $default-heading-color;
                    background-color: #fff;
                    border-color: #fff;
                }
            }
        }
    }
}

.mkd-pvd {
    background-color: #fff;
}

//Product value deal middle

.mkd-pvd-middle {
    padding: 21px 30px 30px 30px;
    text-align: center;
    position: relative;

    .mkd-pli-inner {
        text-align: center;
    }
    .mkd-pvd-title {
        margin: 0 0 9px;
        padding-right: 9%;
        text-align: left;
    }

    .mkd-pvd-compare {
        position: absolute;
        top: 21px;
        right: 20px;

        .compare-button {
            width: inherit;
        }
    }

    .mkd-pvd-category {
        margin: 0 0 8px 0;
        text-align: left;

        a {
            font-size: 13px;
            font-weight: 400;
            color: $additional-text-color;
            text-transform: capitalize;
            &:hover {
                color: $first-main-color;
            }
        }
    }
    .mkd-pvd-price {
        text-align: left;

        .woocommerce-Price-amount {
            margin-right: 11px;
            font-size: 18px;
            font-weight: 500;
            color: $value-deal-color;
            text-decoration: none;
        }

        &.mkd-value-deal {
            ins {
                text-decoration: none;
            }
            del {
                display: none;
            }
        }

        .mkd-value-deal-label {
            font-size: 14px;
            font-weight: 500;
            color: #d2d2d2;
        }
    }

    .mkd-offer {
        text-align: center;
        margin: 31px 0 27px;
        font-size: 13px;
        color: #ff4b4b;
    }

    .mkd-vd-countdown {
        text-align: center;
        .countdown-section {
            position: relative;
            text-align: center;
            .countdown-amount {
                display: inline-block;
                box-sizing: border-box;
                background-color: #f2f3f6;
                margin-right: 9px;
                margin-left: 9px;
                min-width: 43px;
                padding: 8px;
                text-align: center;
                font-size: 18px;
                font-weight: 500;
                color: #000;

                &:after {
                    position: absolute;
                    content: ":";
                    right: -1px;
                    top: -7px;
                }
            }
            &:last-child {
                .countdown-amount {
                    &:after {
                        content: "";
                    }
                }
            }

            &:first-child {
                .countdown-amount {
                    margin-left: 0;
                }
            }
        }
    }
    .mkd-vd-countdown-holder {
        display: inline-block;

        .mkd-vd-countdown {

            .countdown-section {
                position: relative;

                .countdown-amount {
                    display: inline-block;
                    box-sizing: border-box;
                    background-color: #f2f3f6;
                    margin-right: 9px;
                    margin-left: 9px;
                    min-width: 43px;
                    padding: 8px;
                    text-align: center;
                    font-size: 18px;
                    font-weight: 500;
                    color: #000;

                    &:after {
                        position: absolute;
                        content: ":";
                        right: -1px;
                        top: -7px;
                    }
                }
                &:last-child {
                    .countdown-amount {
                        &:after {
                            content: "";
                        }
                    }
                }

                &:first-child {
                    .countdown-amount {
                        margin-left: 0;
                    }
                }
            }
        }

        .mkd-vd-countdown-labels {
            text-align: center;

            h6 {
                text-transform: uppercase;
                font-size: 9px;
            }

            .mkd-vd-countdown-hours {
                width: calc(100% - 130px);
                float: left;
            }

            .mkd-vd-countdown-mins {
                float: left;
                width: 77px;
            }

            .mkd-vd-countdown-secs {
                float: left;
                width: 43px;
            }

        }
    }

    .mkd-pvd-add-to-cart {
        position: relative;
        text-align: right;

        .add_to_cart_button, .product_type_external {
            position: absolute;
            right: 0;
            bottom: 0px;
            width: 43px;
            height: 43px;
            font-size: 0;
            border-radius: 50%;
            padding: 0;
            background-color: #f9f9f9;
            @include mkdTransition(all .2s ease-in-out);

            &:before {
                content: "\6b";
                font-family: "dripicons-v2" !important;
                position: absolute;
                top: 0;
                left: 47%;
                color: #fff;
                font-size: 9px;
                text-transform: none !important;
                opacity: 0;
                pointer-events:none;
                @include mkdTransform(translate(-47%, -100%));
                -webkit-transition: transform .35s cubic-bezier(.53, .01, .18, 1), opacity .3s ease;
                -moz-transition: transform .35s cubic-bezier(.53, .01, .18, 1), opacity .3s ease;
                transition: transform .35s cubic-bezier(.53, .01, .18, 1), opacity .3s ease;
            }

            &:after {
                position: absolute;
                top: 50%;
                left: 47%;
                @include mkdTransform(translate(-47%, -50%));
                content: "\51";
                font-family: "dripicons-v2" !important;
                font-size: 18px;
                color: #8e8e8e;
                pointer-events:none;
                -webkit-transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                -moz-transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                transition: transform .35s cubic-bezier(.53, .01, .18, 1);
            }

            &.added {
                opacity:0;
            }

            &.loading {
                &:before {
                    @include mkdAnimation(mkdAddingToCart .5s infinite ease-in-out);
                }
            }

            &:hover {
                background-color: $first-main-color;

                &:before {
                    @include mkdTransform(translate(-30%, 0));
                    opacity: 1;
                    color: #fff;
                }

                &:after {
                    color: #fff;
                    @include mkdTransform(translate(-50%, -40%));
                    -webkit-transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                    -moz-transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                    transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                }
            }
        }

        .added_to_cart {
            position: absolute;
            right: 0;
            bottom: 0;
            width: 43px;
            height: 43px;
            font-size: 0;
            border-radius: 50%;
            padding: 0;
            background-color: $first-main-color;
            @include mkdTransition(all .2s ease-in-out);

            &:after {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                content: "\53";
                font-family: "dripicons-v2" !important;
                font-size: 18px;
                color: #fff;
            }

        }
    }
    &.mkd-vd-border {
        border: 1px solid $default-border-color;
    }
}

