/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkd-woo-single-page {

    &.mkd-woo-single-thumb-on-left-side {

        .mkd-single-product-content {

            .images {

                @media only screen and (min-width: $ipad-portrait-plus-pixel) {

                    figure {
                        width: 100%;
                        margin: 0;
                    }

                    .woocommerce-product-gallery__image {

                        &:first-child {
                            left: 20.5%;
                            width: 79.5%;
                        }
                    }

                    &.woocommerce-product-gallery--with-images {

                        .woocommerce-product-gallery__image {

                            &:not(:first-child) {
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 20%;
                                padding: 0;
                                box-sizing: border-box;

                                a {
                                    width: 88%;

                                    img {
                                        height: 100%;
                                    }
                                }
                            }

                            $thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
                            @for $i from 0 to length($thumbnail_numbers) {
                                $thumbnail_number_value: nth($thumbnail_numbers, $i+1);

                                &:nth-child(#{$thumbnail_number_value}#{n}) {

                                    $topPos: ($i * 25 + $i)#{'%'};

                                    top: $topPos;
                                }
                            }
                        }
                    }
                }

                .woocommerce-product-gallery__image {

                    a {
                        border: 1px solid $default-border-color;
                        box-sizing: border-box;
                    }

                    &:first-child a {
                        border: none;
                    }
                }
            }
        }

    }

    .product {
        @include mkdRelativeHolderLayout();
        float: none;
        margin: 0;

        //Grouped Product
        &.product-type-grouped {
            margin: 0;
        }
    }

    .mkd-single-product-content {
        @include mkdRelativeHolderLayout();
        clear: both;
        margin: 0 0 80px;

        .images {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            width: 55%;
            margin: 0;
            padding: 0 29px 0 0;
            float: left;
            box-sizing: border-box;

            figure {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0 -10px;

                a {
                    @include mkdRelativeHolderLayout();

                    img {
                        position: relative;
                        width: 100%;
                        display: block;
                    }
                }
            }

            .woocommerce-product-gallery__trigger {
                position: absolute;
                top: 10px;
                right: 39px;
                width: 30px;
                height: 30px;
                line-height: 30px;
                text-align: center;
                z-index: 10;
            }

            .woocommerce-product-gallery__image {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0 10px;
                box-sizing: border-box;

                &:first-child {
                    left: 10px;
                    width: calc(100% - 20px);
                    padding: 0;
                }

                .zoomImg {
                    cursor: crosshair;
                }
            }

            &.woocommerce-product-gallery--with-images {

                .woocommerce-product-gallery__image {

                    &:not(:first-child) {
                        float: left;
                        padding-top: 20px;
                    }
                }
            }

            $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
            @for $i from 0 to length($thumb_3_columns_label) {
                &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
                    $column_width: 100%/($i+2);

                    .woocommerce-product-gallery__image {

                        &:not(:first-child) {
                            width: $column_width;
                        }
                    }
                }
            }
        }

        .mkd-labels-holder {
            position: absolute;
            top: 5px;
            left: 160px;
            span {
                position: relative;
                font-size: 16px;
                font-weight: 500;
                color: #ff4b4b;
            }
        }

        .mkd-out-of-stock {
            top: 6px;
            color: #ff4b4b;
            background-color: transparent;
            letter-spacing: 0;
            font-size: 16px;
            font-weight: 500;

        }
    }

    .mkd-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 43%;
        padding: 0 0 0 29px;
        float: left;
        box-sizing: border-box;

        .mkd-single-product-title {
            display: block;
            margin: 0 0 14px;
            font-weight: 500;

        }

        .price {
            position: relative;
            display: inline-block;
            margin: 4px 19px 0 0;
            padding-bottom: 21px;
            line-height: 1;
            color: $default-heading-color;


            * {
                font-size: inherit;
                line-height: inherit;
            }
            > span {
                font-size: 22px;
                font-weight: 500;
            }

            del {
                font-size: 18px;
                font-weight: 500;
                color: #d2d2d2;
            }

            ins {
                text-decoration: none;
                font-size: 22px;
                font-weight: 500;
                color: #ff4b4b;
            }



        }
        .mkd-price-stock-holder {
            border-bottom: 1px solid $default-border-color;
        }

        .woocommerce-product-rating {
            @include mkdRelativeHolderLayout();
            line-height: 1;
            padding: 15px 0 3px;


            .woocommerce-review-link {
                font-weight: 500;
                color: #d2d2d2;
                margin-left: 3px;
            }
        }

        .woocommerce-product-details__short-description {
            @include mkdRelativeHolderLayout();
            margin: 28px 0 0;

            p,
            ul {
                margin: 0;
            }

            + p.stock.in-stock {
                display: none;
            }
        }

        .product_meta {
            margin: 40px 0 0;

            > span {
                display: block;
                margin: 3px 0;
                font-size: 14px;
                font-weight: 500;
                color: $default-heading-color;

                .sku {
                    color: #b1b1b1;
                    font-weight: 400;
                    text-transform: uppercase;
                }

                a {
                    color: #b1b1b1;
                    font-weight: 400;
                    text-transform: capitalize;

                    &:hover {
                        color: $first-main-color;
                        @include mkdTransition(color .2s ease-in)
                    }
                }
            }
        }

        .mkd-woo-social-share-holder {
            @include mkdRelativeHolderLayout();
            margin: 0;

            > * {
                display: table-cell;
                vertical-align: top;
            }

            > span {
                padding: 0 9px 0 0;
            }
        }

        p.stock.out-of-stock {
            color: $default-heading-color;
            font-weight: bold;
            margin: 18px 0 0;
        }

        p.stock.in-stock {
            display: inline-block;
            font-size: 13px;
            font-weight: 400;
            color: $value-deal-color;
            span {
                color: $additional-text-color;
            }
        }

        form.cart {
            @include mkdRelativeHolderLayout();
            margin: 34px 0 32px;
        }

        .mkd-quantity-buttons {
            margin: 0 -4px 0 0;
        }

        .clear {
            display: none;
        }


        .yith-wcwl-add-button,
        .yith-wcwl-wishlistaddedbrowse,
        .yith-wcwl-wishlistexistsbrowse {

            a {
                margin-right: 24px;
            }
        }


        .compare,
        .compare.button {

            display: inline-block;
            position: relative;
            top: -2px;
            background-color: transparent;
            color: $default-heading-color;
            font-size: 12px;
            font-weight: 500;
            padding: 0;

            &:hover {
                background-color: transparent;
                color: $first-main-color;

                &:after {
                    color: $first-main-color;
                }
            }


            &:after {
                content: "\e040";
                float: left;
                color: $default-heading-color;
                font-family: "dripicons-v2" !important;
                font-size: 16px;
                line-height: 1em;
                border-left: 1px solid $default-border-color;
                padding: 5px 10px 5px 24px;
            }

            &:hover {
                &:after {
                    color: $first-main-color;
                    @include mkdTransition(color .2s ease-in);
                }
            }


        }

        .compare.button {
            &:after {
                padding: 2px 10px 5px 24px;
            }
        }
    }


    .woocommerce-tabs {
        @include mkdRelativeHolderLayout();
        margin: 3px 0 0;

        ul.tabs {
            @include mkdRelativeHolderLayout();
            list-style: none;
            //margin: 0;
            //padding: 0;
            border-bottom: 1px solid $default-border-color;
            margin: 0 -1000px;
            padding: 0 1000px;
            clear: both;

            > li {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                &:first-child a {
                    padding-left: 0;
                }

                a {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    padding: 9px 15px;
                    font-size: 16px;
                    font-weight: 500;

                }

                &.active a,
                &:hover a {
                    color: $first-main-color;
                    position: relative;
                    &:after {
                        content: '';
                        position: absolute;
                        width: 100%;
                        left: 0;
                        bottom: -1px;
                        border-bottom: 2px solid #5677fc;
                        @include mkdTransition(border .3s, ease-in)
                    }
                }
            }


        }

        .woocommerce-Tabs-panel  {
            margin: 0 -1000px;
            padding: 0 1000px;
            background-color: #fafafa;

            .mkd-cross-sells-accessories-tab {
                margin-top: 100px;

                .mkd-cross-sells {
                    margin-bottom: 73px;
                    li.product {

                        .mkd-products-inner {
                            background-color: #fff;
                            padding: 15px 25px;
                        }

                        .compare-button {
                            margin: 0;
                        }

                        .mkd-pl-text-wrapper {
                            margin: 12px 0 10px;

                            .price {
                                margin: 9px 0 0;
                            }
                        }

                        .mkd-pl-add-to-cart-holder {
                            a {
                                &:hover {
                                    &:after {
                                        color: #fff;
                                        @include mkdTransition(color .2s ease-in);
                                    }
                                }
                            }
                        }

                        &:after {
                            width: 0;
                        }
                    }
                }
            }
            p {
                margin-top: 0;
            }
        }

        .entry-content {
            @include mkdRelativeHolderLayout();

            >h2 {
                font-size: 16px;
                font-weight: 500;
                margin: 99px 0 0;

            }

            > p {
                margin: 10px 0 99px;
            }

            .woocommerce-Reviews-title {
                font-size: 16px;
                font-weight: 500;
                span {
                    font-size: 16px;
                    font-weight: 500;
                }
            }
        }

        table {
            margin: 10px 0 96px;

            &.shop_attributes {
                th {
                    font-weight: 500;
                }
            }

            tr, th {
                border: 0;
            }

            td, th {
                padding: 3px 0;
                vertical-align: middle;
                text-align: left;

                p {
                    margin: 0;
                }
            }

            th {
                color: $default-heading-color;
                width: 1%;
                padding: 0 10px 0 0;
            }

            td {
                width: 99%;
            }
        }

        #reviews {
            @include mkdRelativeHolderLayout();
            margin: 87px 0 105px;

            h2 {
                font-size: 18px;
                line-height: 24px;
                margin: 8px 0 24px;
            }

            ol.commentlist {
                list-style: none;

                li {
                    margin: 0 0 15px;

                    &:last-child {
                        margin: 0;
                    }
                }

                .comment_container {
                    @include mkdRelativeHolderLayout();

                    img {
                        float: left;
                        width: 82px;
                        border-radius: 4em;
                    }
                }

                .comment-text {
                    padding: 0 30% 0 108px;

                    p {
                        margin: 0;
                    }

                    .meta {
                        color: #000;
                        strong {
                            font-weight: 500;
                        }
                    }

                    .woocommerce-review__dash {
                        display: none;
                    }

                    .woocommerce-review__published-date {
                        margin: 0;
                        font-size: 13px;
                        font-weight: 400;
                        vertical-align: middle;
                        color: #b5b5b5;
                        padding-left: 4px;
                    }
                }
            }

            .comment-respond {
                margin-top: 15px;
                font-weight: 500;
                color: #000;

                .comment-reply-title {
                    margin: 0 0 10px;
                }

                p {
                    margin: 0;
                }

                .stars {
                    overflow: hidden;

                    a {
                        position: relative;
                        display: inline-block;
                        color: transparent;
                        font-size: 0;
                        margin: 0;

                        &:before {
                            position: relative;
                            display: inline-block;
                            vertical-align: middle;
                            font-family: "ElegantIcons";
                            content: "\e033";
                            font-size: 12px;
                            line-height: 1;
                            letter-spacing: 2px;
                            color: #d2d2d2;
                            overflow: hidden;
                            z-index: 0;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }

                        &.active:after {
                            position: absolute;
                            right: 0;
                            bottom: 7px;
                            display: block;
                            font-family: "ElegantIcons";
                            font-size: 12px;
                            line-height: 1;
                            letter-spacing: 2px;
                            color: #f8e900;
                            content: "\e033\e033\e033\e033\e033";
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }
                }

                .comment-form-comment {
                    width: 50%;
                }
            }
        }
    }

    .mkd-cross-sells {
        & > .products > .product {
            width: 33.33%;

            &:nth-child(3n+1) {
                clear: both;
            }
        }
    }

    .mkd-cross-sells-accessories-tab {
        .mkd-product-item {
            margin: 19px 0 10px;

            &:first-child {
                margin-top: 0;
            }
        }

        .mkd-product-check {
            display: block;
            float: left;
        }

        .mkd-product-title-price {
            padding-left: 30px;
            padding-bottom: 17px;
            border-bottom: 1px solid $default-border-color;

            .amount {
                font-weight: 500;
                color: #ff4b4b;
            }

            ins {
                text-decoration: none;
            }

            del {
                color: #d2d2d2;
                .amount {
                    font-weight: 500;
                    color: #d2d2d2;
                }
            }
        }

        .mkd-product-title {
            margin-top: 0;
        }

        .mkd-total-price-label {
            float: left;
        }

        .mkd-total-price-holder {
            margin: 16px 0 32px;
            border-bottom: 1px solid $default-border-color;
        }


        .mkd-total-price {
            float: right;
            margin-bottom: 10px;

            h2 {
                font-weight: 500;
                color: #ff4b4b;
                margin: 4px 0 0;
            }

            .mkd-total-price-items {
                color: #a1a1a1;
                margin-top: 6px;
            }
        }


        .mkd-total-add-to-cart .button {
            float: right;
            margin-bottom: 50px;

            &.adding {
                pointer-events: none;
            }
        }
    }

    .related.products,
    .upsells.products {

        > h2 {
            display: block;
            position: relative;
            margin: 94px 0 61px;
            padding-bottom: 13px;
            border-bottom: 1px solid $default-border-color;
            text-align: left;
            font-size: 16px;
            font-weight: 500;

            &:after {
                content: '';
                position: absolute;
                bottom: -1px;
                left: 0;
                //width: 141px;
                height: 2px;
                background-color: $first-main-color;
            }
        }

        .product {

            .mkd-pl-text-wrapper {
                margin-bottom: 0;
            }
        }

        .mkd-products-inner {
            .compare-button {
                margin: 0;
            }
        }
    }

    .related.products {
        padding-bottom: 63px;
    }

    .related.products>h2 {
        &:after {
            width: 141px;
        }

    }

    .upsells.products>h2 {
        &:after {
            width: 151px;
        }

    }

//    External Product

    .product-type-external {
        .mkd-single-product-content {
            .mkd-single-product-summary {
                .summary {
                    .cart {
                        margin : 32px 0;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */