/* ==========================================================================
   Product List - Carousel shortcode styles - begin
   ========================================================================== */

.mkd-plc-holder {
    @include mkdRelativeHolderLayout();

    .mkd-plc-outer {
        @include mkdRelativeHolderLayout();
    }

	.mkd-owl-slider {

		.owl-nav .owl-next>span,
		.owl-nav .owl-prev>span {
			font-size: 24px;
			@include mkdTransition(color .25s ease-in-out);

			&:hover {
				color: $first-main-color;
			}
		}

		.owl-nav {
			.owl-next {
				right: -14px;
				border: 1px solid #dfe1ee;
				border-radius: 50%;
				padding: 8px;
				background: #fff;
				color: #c1c0c0;
			}

			.owl-prev {
				left: -14px;
				border: 1px solid #dfe1ee;
				border-radius: 50%;
				padding: 8px;
				background: #fff;
				color: #c1c0c0;
			}
		}
	}

    .mkd-plc-item {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        overflow: hidden;
        box-sizing: border-box;

        &:hover {

            .mkd-plc-image-outer {

                .mkd-plc-text {
                    opacity: 1;
                }
            }
        }

        .touch & {
            cursor: pointer;
        }

		.mkd-plc-text {
			text-align: center;
		}

        .mkd-plc-image-outer {
            @include mkdRelativeHolderLayout();

            .mkd-plc-image {
                @include mkdRelativeHolderLayout();

                .mkd-plc-onsale,
                .mkd-plc-out-of-stock {
                    position: absolute;
                    display: block;
                    top: 0;
                    width: auto;
                    height: 24px;
                    color: #fff;
                    font-size: 8px;
                    line-height: 24px;
                    letter-spacing: 1px;
                    padding: 0 16px;
                    margin: 0;
                    text-align: center;
                    z-index: 100;
                    @include mkdTransform(translateZ(0));
                    box-sizing: border-box;
                }

                .mkd-plc-onsale {
                    left: 0;
	                background-color: rgba($default-heading-color, .5);
                }

                .mkd-plc-out-of-stock {
	                right: 0;
	                background-color: rgba($default-heading-color, .5);
                }
            }

            .mkd-plc-text {
                position: absolute;
                top: 0;
                left: 0;
                display: inline-block;
                width: 100%;
                height: 100%;
                padding: 10px;
                opacity: 0;
                background-color: rgba($default-heading-color, 0.6);
                box-sizing: border-box;
                @include mkdTransition(opacity .2s ease-out, background-color .2s ease-out);
            }

            .mkd-plc-text-outer {
                position: relative;
                display: table;
                width: 100%;
                height: 100%;
                text-align: center;
            }

            .mkd-plc-text-inner {
                position: relative;
                display: table-cell;
                width: 100%;
                height: 100%;
                vertical-align: middle;
            }

			&:hover {
				+ .mkd-plc-text-wrapper h5 > a {
					color: $first-main-color;
				}
			}
        }

        .mkd-plc-title {
			margin: 9px 0 4px;
        }
	
	    .mkd-plc-category {
		    margin: 11px 0 3px;
	    }

        .mkd-plc-excerpt {
            color: $default-heading-color;
            line-height: 1.3em;
            margin: 11px 0 3px;
        }

        .mkd-plc-rating-holder {
            @include mkdRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 3px;
        }

		.mkd-plc-rating {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			font-family: "ElegantIcons";
			font-size: 12px;
			line-height: 1;
			letter-spacing: 5px;
			color: #d2d2d2;
			overflow: hidden;
			-webkit-font-smoothing: antialiased;

			&:before {
				content: "\e033\e033\e033\e033\e033";
			}

			span {
				position: absolute;
				top: 0;
				left: 0;
				overflow: hidden;

				&:before {
					content: "\e033\e033\e033\e033\e033";
					color: #f8e900;
				}
			}
		}

		.mkd-plc-price {
			line-height: 1;
			margin: 9px 0 0;
			font-size: 18px;
			font-weight: 500;
			color: $default-heading-color;

			* {
				font-size: inherit;
				line-height: inherit;
			}

			del {
				margin: 0 8px 0 0;
				font-size: 14px;
				color: #d2d2d2;
				vertical-align: middle;
				display: inline-block;
			}

			ins {
				color: #ff4b4b;
				text-decoration: none;
				vertical-align: middle;
				display: inline-block;
			}
		}
	
	    .button,
	    .added_to_cart {
		    @include mkdButtonDefaultStyle();
		    @include mkdButtonMediumParams();
		    white-space: nowrap;
		    z-index: 3;
	    }
	
	    .button.added {
		    display: none;
	    }
	
	    .mkd-plc-add-to-cart {
		    margin: 20px 0 0;
		
		    &.mkd-default-skin {
			
			    .button,
			    .added_to_cart {
				    @include mkdButtonSolidColor();
				
				    &:hover {
					    @include mkdButtonSolidHoverColor();
				    }
			    }
		    }
		
		    &.mkd-light-skin {
			
			    .button,
			    .added_to_cart {
				    color: $default-heading-color;
				    background-color: #fff;
				
				    &:hover {
					    color: #fff;
					    background-color: $first-main-color;
				    }
			    }
		    }
		
		    &.mkd-dark-skin {
			
			    .button,
			    .added_to_cart {
				    color: #fff;
				    background-color: $default-heading-color;
				
				    &:hover {
					    color: #fff;
					    background-color: $first-main-color;
				    }
			    }
		    }
	    }

        .mkd-plc-link {
            @include mkdAbsoluteHolderLayout();
        }
    }

    &.mkd-standard-layout {

    }

    &.mkd-simple-layout {

    }
	
	&.mkd-plc-nav-light-skin {
		
		.owl-nav {
			
			.owl-prev,
			.owl-next {
				color: $header-light-color;
				background: $header-dark-color;
				
				&:hover {
					color: $header-light-hover-color;
				}
			}
		}
	}
	
	&.mkd-plc-pag-inside-slider {
		
		.owl-prev,
		.owl-next {
			@include mkdTransform(translateY(-50%));
		}
		
		.owl-dots {
			position: absolute;
			left: 0;
			bottom: 10px;
			margin: 0;
		}
	}
	
	&.mkd-plc-pag-light-skin {
		
		.owl-dots {
			
			.owl-dot {

				span {
					background-color: $header-light-hover-color;
				}

				&:hover,
				&.active {

					span {
						background-color: $header-light-color;
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Product List - Carousel shortcode styles - end
   ========================================================================== */