/* ==========================================================================
   Product List Advanced shortcode styles - begin
   ========================================================================== */

.mkd-pl-holder.mkd-pl-holder-advanced {

    //filters style - START

    .mkd-pl-filters {
        border-bottom: 1px solid $default-border-color;
        margin-bottom: 32px;
    }

    .mkd-pl-categories {
        float: left;

        ul {
            margin: 0;

            li {
                list-style-type: none;
                display: inline-block;
                font-size: 16px;
                font-weight: 500;
                line-height: 1.5em;
                margin-right: 31px;
                @include mkdTransition(color .3s, ease-in);

                &:last-child {
                    margin-right: 0;
                }

                a.active {
                    position: relative;
                }

                a.mkd-item-active,
                a:hover {
                    color: $first-main-color;
                    position: relative;
                }

                &.mkd-filters-line {
                    position:absolute;
                    bottom:0;
                    height:2px;
                    background-color:$first-main-color;
                    @include mkdTransition(all .2s ease-in-out);
                }
            }
        }
    }

    .mkd-pl-ordering-outer {
        float: right;
        height: 25px;

        h5 {
            margin: 0;
        }

        .mkd-pl-ordering {
            background-color: #000;
            display: inline-block;
            padding: 35px 20px;
            position: absolute;
            right: 0;
            top: 25px;
            width: 365px;
            z-index: 101;
            opacity: 0;
            visibility: hidden;

            ul {
                list-style-type: none;
            }

            > div {
                display: inline-block;
                width: 140px;
                margin: 0 20px;
            }

            h5 {
                color: #fff;
            }

            ul {
                li {
                    a {
                        color: $additional-text-color;

                        &:hover {
                            color: #fff;
                        }
                    }
                }
            }
        }

        &:hover .mkd-pl-ordering {
            visibility: visible;
            opacity: 1;
        }
    }

    //filters style - END

    &.mkd-masonry-type .mkd-pli .mkd-pla-inner {
        background-color: #fff;
        overflow: hidden;
    }

    &.mkd-standard-type .mkd-pli:after {
        content: '';
        position: absolute;
        top: 0;
        left: 100%;
        width: 1px;
        height: 100%;
        background-color: $default-border-color;
    }

    &.mkd-light-filter-skin {
        .mkd-pl-categories ul li a {
            color: #fff;
        }

        .mkd-pl-categories .mkd-item-active,
        .mkd-pl-categories ul li a:not(.active):hover {
            color:$first-main-color;
        }

        .mkd-pl-filters {
            border-bottom: 1px solid #5d5e62;
        }

        .mkd-pl-ordering-outer h5 {
            color: #fff;
        }
    }

    .mkd-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        z-index: 100;
        box-sizing: border-box;

        .mkd-pla-inner {
            height: 100%;
            position: relative;

            .mkd-pla-title {
                margin: 0 0 12px 0;
            }
        }



        .mkd-pla-image {

            position: relative;

            img {
                display: block;
            }

            .mkd-pli-link {
                @include mkdAbsoluteHolderLayout();
                z-index: 9999;
            }
        }

        .mkd-pla-inner-info {
            padding: 16px 26px;

            .mkd-pla-new-product,
            .mkd-pla-onsale {
                font-weight: 500;
                color: #ff4b4b;
            }

            .mkd-pli-text-wrapper {
                padding-top: 0;
            }
        }

        .mkd-pla-category {
            margin: 0 0 5px 0;

            a {
                font-size: 13px;
                font-weight: 400;
                color: $additional-text-color;
                text-transform: capitalize;

                &:hover {
                    color: $first-main-color;
                    @include mkdTransition(color .3s ease-out);
                }
            }
        }

        &.mkd-pli-out-of-stock-holder {

            .mkd-pla-inner {

                .mkd-pla-image {
                    img {
                        opacity: .1;
                    }

                    .mkd-pla-out-of-stock {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        vertical-align: middle;
                        transform: translate(-50%, -50%);
                        right: auto;
                        background: transparent;
                        display: block;
                        width: auto;
                        height: auto;
                        color: #ff4b4b;
                        font-size: 14px;
                        font-weight: 500;
                        letter-spacing: 0;
                        text-transform: capitalize;
                        padding: 0;
                        margin: 0;
                        text-align: center;
                        z-index: 100;
                        box-sizing: border-box;

                    }
                }
            }

            .mkd-pla-add-to-cart {

                a {
                    font-size: 0;
                    border-radius: 50%;
                    padding: 0;
                    width: 43px;
                    height: 43px;
                    position: relative;
                    background: #f9f9f9;

                    &:after {
                        color: #ff4b4b;
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        content: "\e067";
                        font-family: "dripicons-v2" !important;
                        font-size: 18px;
                    }
                }
            }
        }

        &.mkd-pla-position-left {
            .mkd-pla-inner-info {
                display: table;
                height: 100%;
            }

            .mkd-pla-image {
                display: table-cell;
                vertical-align: middle;
                width: 28%;

                &:hover {
                    + .mkd-pla-text-wrapper> h5 > a {
                        color: $first-main-color;
                    }
                }
            }

            .mkd-pla-text-wrapper {
                display: table-cell;
                vertical-align: middle;
                width: 72%;
                padding: 0 55px 0 15px;
            }
        }

        &.mkd-pla-position-middle {
            .mkd-pla-image {
                margin: 15px 0 24px;

                &:hover {
                    + .mkd-pli-text-wrapper> h5 > a {
                        color: $first-main-color;
                    }
                }
            }


            .mkd-pli-text-wrapper {
                margin-top: 0;
            }

            .mkd-pla-title {
                padding-right: 33%;

            }
        }

        .mkd-pla-cv-holder {
            position: absolute;
            top: 18px;
            right: 23px;

            > div {
                display: inline-block;
                width: auto;
                vertical-align: middle;
            }
        }

        .yith-wcwl-add-button a:after {
            padding: 0 0 0 8px;
        }

        .mkd-pla-add-to-cart {
            position: absolute;
            right: 26px;
            bottom: 20px;

            .added_to_cart {
                display: inline-block;
                font-size: 0;
                border-radius: 50%;
                padding: 0;
                width: 43px;
                height: 43px;
                position: absolute;
                bottom:0;
                right:0;
                background-color: $first-main-color;
                color: #fff;
                @include mkdTransition(all .2s ease-in-out);

                &:after {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    content: "\53";
                    font-family: "dripicons-v2" !important;
                    font-size: 18px;
                }
            }

            .add_to_cart_button, .product_type_external {
                display: inline-block;
                font-size: 0;
                border-radius: 50%;
                padding: 0;
                width: 43px;
                height: 43px;
                position: relative;
                background-color: #f9f9f9;
                @include mkdTransition(all .2s ease-in-out);

                &:before {
                    content: "\6b";
                    font-family: "dripicons-v2" !important;
                    position: absolute;
                    top: 0;
                    left: 47%;
                    color: #fff;
                    font-size: 9px;
                    text-transform: none !important;
                    opacity: 0;
                    pointer-events:none;
                    @include mkdTransform(translate(-47%, -100%));
                    -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
                    -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
                    transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
                }

                &:after {
                    position: absolute;
                    top: 50%;
                    left: 47%;
                    @include mkdTransform(translate(-47%, -50%));
                    content: "\51";
                    font-family: "dripicons-v2" !important;
                    font-size: 18px;
                    color: #8e8e8e;
                    pointer-events:none;
                    -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                    -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                    transition: transform .35s cubic-bezier(.53,.01,.18,1);
                }

                &.loading {
                    &:before {
                        @include mkdAnimation(mkdAddingToCart .5s infinite ease-in-out);
                    }
                }

                &:hover {
                    background-color: $first-main-color;

                    &:before {
                        @include mkdTransform(translate(-30%, 0));
                        opacity: 1;
                        color: #fff;
                    }

                    &:after {
                        color: #fff;
                        @include mkdTransform(translate(-50%, -40%));
                        -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                        -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                        transition: transform .35s cubic-bezier(.53,.01,.18,1);
                    }
                }

                &.added {
                    opacity:0;
                }
            }

        }

        .mkd-pla-price {
            line-height: 1;
            font-size: 18px;
            font-weight: 500;
            color: $default-heading-color;

            * {
                font-size: inherit;
                line-height: inherit;
            }

            del {
                margin: 0 8px 0 0;
                font-size: 14px;
                color: #d2d2d2;
                vertical-align: middle;
                display: inline-block;
            }

            ins {
                color: #ff4b4b;
                text-decoration: none;
                vertical-align: middle;
                display: inline-block;
            }

            &.mkd-value-deal {

                ins {
                    color: $value-deal-color;
                }


                del {
                    display: none;
                }
            }

            .mkd-value-deal-label {
                font-size: 12px;
                font-weight: 400;
                color: #d2d2d2;
            }
        }
    }
}

/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */