/* ==========================================================================
   Product List - Simple shortcode styles - begin
   ========================================================================== */

.mkd-pls-holder {
	@include mkdRelativeHolderLayout();

	> ul {
		@include mkdRelativeHolderLayout();
		list-style: none;
		margin: 0;
		padding: 0;

		> li {
			@include mkdRelativeHolderLayout();
			padding: 0;
			margin: 0 0 20px;

			&:last-child {
				margin: 0;
			}
		}
	}
	
	.mkd-pls-image {
		display: table-cell;
		vertical-align: top;
		width: 84px;

		a, img {
			display: block;
		}

		&:hover {
			+ .mkd-pls-text {
				a {
					color: $first-main-color;
				}
			}
		}

	}
	
	.mkd-pls-text {
		display: table-cell;
		vertical-align: middle;
		padding: 0 0 0 20px;

		
		.mkd-pls-title {
			margin: 0 0 4px;

			a {
				&:hover {
					color: $first-main-color;
				}
			}
		}
		
		.mkd-pls-rating-holder {
			@include mkdRelativeHolderLayout();
			line-height: 1;
			margin: 5px 0 3px;
		}
		
		.mkd-pls-rating {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			font-family: "ElegantIcons";
			font-size: 12px;
			line-height: 1;
			letter-spacing: 2px;
			color: #d2d2d2;
			overflow: hidden;
			-webkit-font-smoothing: antialiased;
			
			&:before {
				content: "\e033\e033\e033\e033\e033";
			}
			
			span {
				position: absolute;
				top: 0;
				left: 0;
				overflow: hidden;
				color: #f8e900;

				&:before {
					content: "\e033\e033\e033\e033\e033";
				}
			}
		}
		
		.mkd-pls-price {
			line-height: 1;
			margin: 14px 0 0;
			color: $default-heading-color;
            font-size: 16px;
            font-weight: 500;
			
			* {
				font-size: inherit;
				line-height: inherit;
			}
			
			del {
				margin: 0 8px 0 0;
				color: #d2d2d2;
				span.woocommerce-Price-amount {
					font-size: 14px ;
				}
			}

			ins {
				color: #ff4b4b;
				text-decoration: none;
			}

            &.mkd-value-deal {
				ins {
					color: $value-deal-color;
				}

                del {
                    display: none;
                }
            }

            .mkd-value-deal-label {
                font-size: 12px;
                font-weight: 500;
                color: #d2d2d2;
            }
		}
	}
}
/* ==========================================================================
   Product List - Simple shortcode styles - end
   ========================================================================== */