/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.mkd-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.mkd-content .woocommerce {
    @include mkdRelativeHolderLayout();
}

.mkd-pl-main-holder {
    margin: 0 0 22px;

    ul.products {

        > .product {

            .button,
            .added_to_cart {
                margin: 0;
            }
        }
    }
}

ul.products {
    list-style: none;
    margin: 0 -10px;
    clear: both;
    overflow: hidden;

    > .product {
        position: relative;
        float: left;
        display: inline-block;
        vertical-align: middle;
        padding: 0 10px;
        margin: 0 0 20px;
        box-sizing: border-box;

        &:after {
            content: '';
            position: absolute;
            left: 100%;
            top: 0;
            height: 100%;
            width: 1px;
            background-color: $default-border-color;
        }

        &:last-child:after {
            display: none;
        }

        .mkd-products-inner {

            > a {
                color: $additional-text-color;
                font-size: 13px;
                font-weight: 400;
                display: inline-block;
                vertical-align: middle;

                &:hover {
                    color: $first-main-color;
                }
            }

            .yith-wcwl-add-to-wishlist {
                float: right;

                & + div {
                    display: none;
                }
            }

            .compare-button {
                width: auto;
                display: inline-block;
                float: right;
            }

            .compare-button .compare {
                font-size: 0;
                background-color: transparent;
                padding: 0;

                &:hover {
                    background-color: transparent;

                    &:after {
                        color: $first-main-color;
                    }
                }

                &.added {
                    color: $first-main-color;
                    display: inline-block;

                    &:after {
                        content: "\e040";
                        color: $first-main-color;
                        font-family: "dripicons-v2" !important;
                        font-size: 16px;
                        padding: 0;
                    }
                }

                &:after {
                    content: "\e040";
                    color: $additional-text-color;
                    font-family: "dripicons-v2" !important;
                    font-size: 16px;
                    padding: 0;
                }
            }

            .touch & {
                cursor: pointer;
            }

            .mkd-product-list-title {
                margin: 7px 0 4px;
            }

            .mkd-pl-rating-holder {
                position: absolute;
                display: block;
                width: 100%;
                top: 0;
                left: 0;
                line-height: 1;
                margin: 10px 0 3px;

                .star-rating {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                    font-family: "ElegantIcons";
                    font-size: 12px;
                    line-height: 1;
                    letter-spacing: 5px;
                    color: #d2d2d2;
                    overflow: hidden;
                    -webkit-font-smoothing: antialiased;

                    &:before {
                        content: "\e033\e033\e033\e033\e033";
                    }

                    span {
                        position: absolute;
                        top: 0;
                        left: 0;
                        overflow: hidden;

                        &:before {
                            content: "\e033\e033\e033\e033\e033";
                            color: #f8e900;
                        }
                    }
                }
            }

            .price {
                line-height: 1;
                margin: 14px 0 0;
                font-size: 18px;
                font-weight: 500;
                color: $default-heading-color;
                display: block;

                * {
                    font-size: inherit;
                    line-height: inherit;
                }

                del {
                    margin: 0 8px 0 0;
                    font-size: 14px;
                    color: #d2d2d2;
                    vertical-align: middle;
                    display: inline-block;
                }

                ins {
                    color: #ff4b4b;
                    text-decoration: none;
                    vertical-align: middle;
                    display: inline-block;
                }
            }


            .mkd-pl-inner {
                @include mkdRelativeHolderLayout();
                overflow: hidden;

                .mkd-pl-image {
                    @include mkdRelativeHolderLayout();
                    overflow: hidden;
                    padding-top: 35px;

                    .mkd-labels-holder {
                        position: absolute;
                        display: block;
                        top: 0;
                        width: auto;

                        .mkd-pl-new-product,
                        .mkd-pl-onsale {
                            position: relative;
                            display: inline-block;
                            top: 0;
                            width: auto;
                            height: 24px;
                            color: #ff4b4b;
                            font-size: 14px;
                            font-weight: 500;
                            line-height: 24px;
                            letter-spacing: 0;
                            text-transform: capitalize;
                            padding: 0;
                            margin: 0;
                            text-align: center;
                            z-index: 100;
                            box-sizing: border-box;
                        }

                        .mkd-pl-onsale {
                            background: transparent;
                        }
                    }

                    img {
                        display: block;
                        width: 100%;
                    }
                }

                > a {
                    @include mkdAbsoluteHolderLayout();
                }

                &:hover {
                    + .mkd-pl-text-wrapper h5 > a {
                        color: $first-main-color;
                    }
                }
            }

            .mkd-pl-text-wrapper {
                @include mkdRelativeHolderLayout();
                margin: 22px 0 10px;
                text-align: left;
                display: table;
                height: 100%;
                padding-top: 26px;

                .mkd-pl-text-left-holder {
                    display: inline-block;
                }

                .mkd-pl-add-to-cart-holder {
                    vertical-align: bottom;
                    display: table-cell;
                    height: 100%;
                    text-align: right;

                    .product_type_grouped {
                        font-size: 0;
                        border-radius: 50%;
                        padding: 0;
                        width: 43px;
                        height: 43px;
                        position: relative;
                        background-color: #f9f9f9;

                        &:after {
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                            font-family: "dripicons-v2" !important;
                            content: "\e037";
                            font-size: 18px;
                            color: #8e8e8e;
                        }
                        &:hover {
                            background-color: $first-main-color;
                            &:after {
                                color: #fff;
                            }
                        }
                    }
                }
            }
        }

        .button.added {
            display: none;
        }

        .added_to_cart {
            font-size: 0;
            border-radius: 50%;
            padding: 0;
            width: 43px;
            height: 43px;
            position: relative;

            &:after {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                content: "\53";
                font-family: "dripicons-v2" !important;
                font-size: 18px;
            }
        }

        .add_to_cart_button, .product_type_external {
            font-size: 0;
            border-radius: 50%;
            padding: 0;
            width: 43px;
            height: 43px;
            position: relative;
            background-color: #f9f9f9;

            &:before {
                content: "\6b";
                font-family: "dripicons-v2" !important;
                position: absolute;
                top: 0;
                left: 47%;
                color: #fff;
                font-size: 9px;
                text-transform: none !important;
                opacity: 0;
                @include mkdTransform(translate(-47%, -100%));
                -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
                -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
                transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
            }

            &:after {
                position: absolute;
                top: 50%;
                left: 47%;
                @include mkdTransform(translate(-47%, -50%));
                content: "\51";
                font-family: "dripicons-v2" !important;
                font-size: 18px;
                color: #8e8e8e;
                -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                transition: transform .35s cubic-bezier(.53,.01,.18,1);
            }
            &:hover {

                &:before {
                    @include mkdTransform(translate(-30%, 0));
                    opacity: 1;
                    color: #fff;
                }

                &:after{
                    color: #fff;
                    @include mkdTransform(translate(-50%, -40%));
                    -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                    -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                    transition: transform .35s cubic-bezier(.53,.01,.18,1);
                }
            }


        }

        .product_type_external {
            &:before {
                content: "";
            }

            &:after {
                left: 48%;
                @include mkdTransform(translate(-48%, -50%));
                content: "\6d";
                text-transform: none;
            }

            &:hover {
                &:after {
                    color: #fff;
                    @include mkdTransform(translate(-40%,-50%));
                }
            }
        }

        &.outofstock {

            .mkd-pl-inner {

                .mkd-pl-image {
                    img {
                        opacity: .1;
                    }

                    .mkd-out-of-stock {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        vertical-align: middle;
                        transform: translate(-50%, -50%);
                        right: auto;
                        background: transparent;
                        display: block;
                        width: auto;
                        height: 24px;
                        color: #ff4b4b;
                        font-size: 14px;
                        font-weight: 500;
                        line-height: 24px;
                        letter-spacing: 0;
                        text-transform: capitalize;
                        padding: 0;
                        margin: 0;
                        text-align: center;
                        z-index: 100;
                        box-sizing: border-box;
                    }
                }

                &:hover {
                    + .mkd-pl-text-wrapper h5  >a {
                        color: $default-heading-color;
                    }
                }
            }

            .mkd-pl-text-wrapper {

                .button {
                    font-size: 0;
                    border-radius: 50%;
                    padding: 0;
                    width: 43px;
                    height: 43px;
                    position: relative;
                    background: #f9f9f9;

                    &:after {
                        color: #ff4b4b;
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        content: "\e067";
                        font-family: "dripicons-v2" !important;
                        font-size: 18px;
                    }
                }
            }
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'large');
$columns_space_width: (0, 5, 10, 15, 25);

@for $i from 0 to length($columns_space_label) {
    &.mkd-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width, $i+1);
        $column_width_margin: $column_width * 2;

        ul.products {
            @if ($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }

            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.mkd-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {

    $product_list_columns: ('columns-3', 'columns-4');

    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);

        > .products {

            > .product {

                @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                    .mkd-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;

                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }

                        &:nth-child(#{$i+3}n) {

                            &:after {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {

    &.columns-1 {

        > .products {

            > .product {
                float: none;
                width: 100%;
            }
        }
    }

    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');

    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);

            > .products {

                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};

                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */