/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

@keyframes mkdAddingToCart {
  0%, 100% {
    @include mkdTransform(translate3d(-30%, 0,0));
  }

  50% {
    @include mkdTransform(translate3d(-30%, 3px,0));
  }
}

.mkd-pl-holder {
    @include mkdRelativeHolderLayout();

    &.mkd-filter-position-left {

        .mkd-pl-outer-holder {
            overflow: hidden;
            width: 75%;
            box-sizing: border-box;
            float: left;
            padding-left: 25px;
        }

        .mkd-pl-filters {
            float: left;

            list-style-type: none;
            width: 25%;
            box-sizing: border-box;

            .mkd-pl-filters-inner {
                border: 1px solid $default-border-color;
                padding: 43px 43px 0;
            }

            .mkd-product-list-sidebar-holder {
                margin-top: 25px;
            }

            h4 {
                margin-bottom: 22px;
                margin-top: 43px;
            }

            > div:first-child {
                h4 {
                    margin-top: 0;
                }
            }

            ul {
                list-style-type: none;
                margin: 15px 0 40px;

            }

            li, ul li {
                padding: 2px 0;

                a {
                    color: $widget-text-color;
                    font-size: 13px;
                    font-weight: 400;
                    position: relative;

                    &:before,
                    &:after {
                        content: '';
                        position: absolute;
                        width: 100%;
                        left: 0;
                        bottom: -3px;
                        border-bottom: 2px solid #97a8fd;
                        opacity: 0;
                        @include mkdTransformOrigin(100% 50%);
                    }

                    &:before {
                        @include mkdTransform(scaleX(0));
                        @include mkdTransformOrigin(0 50%);
                        -webkit-transition: -webkit-transform .0s .2s;
                        transition: transform .0s .2s;

                    }

                    &:after {
                        @include mkdTransform(scaleX(0));
                        opacity: 0;
                        -webkit-transition: -webkit-transform .2s, opacity .0s .2s;
                        transition: transform .2s, opacity .0s .2s;
                    }

                    &:hover {
                        color: $first-main-color;
                        &:before {
                            opacity: 1;
                            @include mkdTransform(scaleX(1));
                            @include mkdTransition(all .2s ease-in-out);
                        }

                        &:after {
                            opacity: 1;
                            @include mkdTransform(scaleX(1));
                            @include mkdTransition(all 0s .2s);
                        }
                    }

                    &:hover, &.mkd-item-active {
                        color: $first-main-color;
                    }
                }
            }

        }
        .mkd-pl-outer {
            p.mkd-no-posts {
                padding: 17px 30px;
                border: 1px solid $default-border-color;
                line-height: 38px;
                margin: 0;
            }
        }
    }

    &.mkd-filter-position-top {

        .mkd-pl-outer-holder {
            margin-top: 30px;
        }

        .mkd-pl-filters {
            list-style-type: none;

            .mkd-pl-layouts-order-holder {
                border: 1px solid $default-border-color;
                position: relative;
                height: 25px;
                display: block;
                padding: 14px;
                background-color: #fafafa;
            }

            ul {
                list-style-type: none;
                padding-bottom: 20px;
            }

            .mkd-pl-order-filter-holder {
                float: right;

                &:hover {
                    .mkd-pl-ordering-outer {
                        opacity: 1;
                        visibility: visible;
                        transition-delay: 0s;
                        transition-duration: .3s;
                    }
                }

                h6 {
                    text-align: right;
                    font-weight: 400;
                    font-size: 12px;
                    color: #908989;
                    margin: 0;
                    text-transform: capitalize;
                    padding-bottom: 20px;
                    display: inline-block;
                    vertical-align: middle;
                    line-height: 2.2em;
                    cursor: pointer;

                    &:after {
                        content: "\54";
                        position: relative;
                        display: inline-block;
                        vertical-align: middle;
                        font-family: "dripicons-v2" !important;
                        font-size: 18px;
                        color: $widget-text-color;
                        line-height: 1em;
                        margin-left: 8px;
                    }
                }

                .mkd-pl-ordering-outer {
                    position: absolute;
                    background-color: #fff;
                    display: inline-block;
                    padding: 25px 20px 0;
                    box-shadow: 0 9px 19.76px 0.24px rgba(87, 98, 101, .06);
                    right: 0;
                    top: 54px;
                    width: 365px;
                    z-index: 101;
                    opacity: 0;
                    visibility: hidden;
                    @include mkdTransition(visibility 0s ease .3s, opacity .3s ease);

                    h4 {
                        color: #000;
                    }

                    ul {
                        li {
                            a {
                                color: $additional-text-color;
                                font-size: 12px;

                                &:hover {
                                    color: $first-main-color;
                                }
                            }
                        }
                    }
                }
            }

            .mkd-pl-layouts {
                float: left;

                h4 {
                    display: none;
                }

                ul {
                    margin: 0;

                    li {
                        display: inline-block;
                        vertical-align: middle;

                        a {
                            vertical-align: middle;

                            &:after {
                                vertical-align: middle;
                            }

                            &.active {
                                &:after {
                                    color: $first-main-color !important;
                                }
                            }
                        }

                        &:nth-child(1) {
                            padding: 0px 16px 0 6px;

                            &:hover a:after {
                                color: $first-main-color;
                            }

                            a {
                                font-size: 0;

                                &:after {
                                    content: "\e05a";
                                    font-family: "dripicons-v2" !important;
                                    color: #a1a4b0;
                                    font-size: 24px;
                                }
                            }
                        }

                        &:nth-child(2) {
                            padding: 0 18px 0 0;

                            &:hover a:after {
                                color: $first-main-color;
                            }

                            a {
                                font-size: 0;

                                &:after {
                                    content: "\63";
                                    font-family: "dripicons-v2" !important;
                                    color: #a1a4b0;
                                    font-size: 20px;
                                }
                            }
                        }

                        &:nth-child(3) {
                            &:hover a:after {
                                color: $first-main-color;
                            }

                            a {
                                font-size: 0;

                                &:after {
                                    content: "\e05b";
                                    font-family: "dripicons-v2" !important;
                                    color: #a1a4b0;
                                    font-size: 20px;
                                }
                            }
                        }
                    }
                }
            }

            .mkd-pl-categories {
                clear: both;
                margin: 25px 0;

                h4 {
                    display: none;
                }

                ul {
                    position:relative;
                    border-bottom: 1px solid $default-border-color;

                    li {
                        display: inline-block;
                        overflow: hidden;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 2em;
                        padding: 0 12px;
                        text-transform: capitalize;

                        &:first-child {
                            padding-left: 0;
                        }

                        &:last-child {
                            padding-right: 0;
                        }

                        a {
                            position: relative;

                            &:before,
                            &:after {
                                content: '';
                                position: absolute;
                                width: 100%;
                                left: 0;
                                bottom: -2px;
                                border-bottom: 2px solid $first-main-color;
                                opacity: 0;
                                transform-origin: 100% 50%;
                            }

                            &:before {
                                @include mkdTransform(scaleX(0));
                                transform-origin: 0 50%;
                                -webkit-transition: transform 0s .2s;
                                -moz-transition: transform 0s .2s;
                                transition: transform 0s .2s;
                            }

                            &:after {
                                @include mkdTransform(scaleX(0));
                                opacity: 0;
                                -webkit-transition: transform .2s,opacity 0s .2s;
                                -moz-transition: transform .2s,opacity 0s .2s;
                                transition: transform .2s,opacity 0s .2s;
                            }

                            &:hover {

                                color: $first-main-color;

                                &:before {
                                    @include mkdTransform(scaleX(1));
                                    opacity: 1;
                                    @include mkdTransition(all .2s ease-in-out);
                                }


                                &:after {
                                    @include mkdTransform(scaleX(1));
                                    opacity: 1;
                                    @include mkdTransition(all 0s .2s);
                                }
                            }

                            &.mkd-item-active {
                                color: $first-main-color;
                                @include mkdTransition(color .3s, ease-in);
                            }

                        }
                    }
                }
            }

            .mkd-pl-sort-filter,
            .mkd-pl-range-filter {
                display: inline-block;
                width: 140px;
                margin: 0 20px;
            }
        }
    }

    .mkd-pli.mkd-pli-out-of-stock-holder {

        .mkd-pli-inner {

            .mkd-pli-image {
                img {
                    opacity: .1;
                }

                .mkd-pli-out-of-stock {
                    position: absolute;
                    top: calc(50% + 19px);
                    left: 50%;
                    vertical-align: middle;
                    @include mkdTransform(translate3d(-50%, -50%,0));
                    right: auto;
                    background: transparent;
                }
            }

            &:hover {
                + .mkd-pli-text-wrapper h5 > a {
                    color: $default-heading-color;
                }
            }
        }

        .mkd-pli-add-to-cart {

            .button {
                font-size: 0;
                border-radius: 50%;
                padding: 0;
                width: 43px;
                height: 43px;
                position: relative;
                background-color: #f9f9f9;

                &:before {
                    content: "";
                }

                &:after {
                    color: #ff4b4b;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    @include mkdTransform(translate3d(-50%, -50%,0));
                    content: "\e067";
                    font-family: "dripicons-v2" !important;
                    font-size: 18px;

                }

                &:hover {
                    background-color: #f9f9f9;

                    &:after {
                        color: #ff4b4b;
                        @include mkdTransform(translate3d(-50%, -50%,0));
                    }
                }

            }
        }
    }

    &.mkd-pli-separator-layout {

        .mkd-pli {
            &:after {
                content: '';
                position: absolute;
                left: 100%;
                top: 0;
                height: 100%;
                width: 1px;
                background-color: $default-border-color;
            }

            &:last-child:after {
                display: none;
            }
        }
    }

    &.mkd-pli-boxed-layout {
        .mkd-pli {
            .mkd-pli-holder {
                padding: 20px;
                border: 1px solid $default-border-color;
            }
        }
    }

    &.mkd-masonry-type {

        //remove top padding from first product list item element in masonry list
        &.mkd-filter-position-left {
            .mkd-pl-outer .mkd-pli.mkd-pli-list-layout:nth-child(3) {
                padding-top: 0;
            }
        }

    }

    &.mkd-pl-masonry-gallery-layout {

        .mkd-pli {
            @include mkdTransition(opacity .2s ease-in-out 0.5s);
        }

        .mkd-pl-outer {
            @include mkdTransition(height .2s ease-in-out .4s);
        }

        .mkd-rotate-circles {
            position:absolute;
            top:50%;
            left:50%;
            opacity:0;
            width:80px;
            @include mkdTransform(translate3d(-50%,-50%,0));
            @include mkdTransition(opacity .2s ease-in-out);

            > div {
                width:18px;
                height:18px;
                background-color: $first-main-color;
            }
        }

        ul {
            position:relative;
        }

        &.mkd-pl-loading {
            .mkd-pli { 
                opacity:0;
                @include mkdTransition(opacity .2s ease-in-out 0s);
            }

            .mkd-rotate-circles { 
                opacity:1;
            }
        }
    }

    //list layout start

    &.mkd-pli-boxed-layout {
        .mkd-pl-outer .mkd-pli.mkd-pli-list-layout {
            border-bottom: 0;
            padding: 20px 0;

            &:first-child {
                padding-top: 0;

            }
        }
    }

    .mkd-pli.mkd-pli-list-layout {
        border-bottom: 1px solid $default-border-color;
        padding-top: 50px;
        padding-bottom: 50px;
        margin: 0;

        .mkd-pli-rating-holder {
            position: relative;
            top: auto;
            left: auto;
        }

        .mkd-pli-add-to-cart .add_to_cart_button,
        .mkd-pli-add-to-cart .added_to_cart,
        &.mkd-pli-out-of-stock-holder .mkd-pli-add-to-cart .button,
        .mkd-pli-add-to-cart .button.product_type_external {
            font-size: 12px;
            border-radius: 30px;
            padding: 8px 20px;
            height: auto;
            position: relative;
            color: #8e8e8e;
            width: 147px;
        }

        &.mkd-pli-out-of-stock-holder .mkd-pli-add-to-cart .button {
            &:after {
                content: '';
            }
            &:hover {
                color: #fff;
                background-color: $first-main-color;
            }
        }

        .mkd-pli-add-to-cart .button.product_type_external {
            &:before,
            &:after {
                content: " ";
            }

            &:hover {
                color: #fff;
            }

        }

        .mkd-pli-add-to-cart,
        &.mkd-pli-out-of-stock-holder .mkd-pli-add-to-cart {
            display: block;
            text-align: center;
            margin-bottom: 26px;
            position: relative;
            bottom: auto;
            right: auto;

            .added_to_cart {
                color: #fff;

                &:after {
                    content: '';
                }

                &:before {
                    position: relative;
                    content: "\53";
                    font-family: dripicons-v2 !important;
                    font-size: 18px;
                    color: #fff;
                    display: inline-block;
                    vertical-align: top;
                    padding-right: 10px;
                }
            }


            .add_to_cart_button {

                @include mkdTransition(color .3s ease-in-out, background-color .3s ease-in-out);

                &:after {
                    content: '';
                }

                &:before {
                    position: relative;
                    left: 0;
                    opacity: 1;
                    content: "\51";
                    font-family: dripicons-v2 !important;
                    font-size: 18px;
                    color: #8e8e8e;
                    display: inline-block;
                    vertical-align: top;
                    padding-right: 10px;
                    @include mkdTransform(none);
                    @include mkdTransition(color .3s ease-in-out);
                }

                &:hover {
                    color: #fff;
                    &:before {
                        color: #fff;
                        @include mkdTransform(none);
                    }
                }
            }

        }

        .mkd-pli-holder {
            display: table;
        }

        .mkd-pli-price {
            margin: 14px 0 28px;
        }

        .mkd-pli-stock-availability {
            font-weight: 400;
            color: $additional-text-color;
            font-size: 13px;

            p {
                display: inline-block;
                margin: 0;
                color: $value-deal-color;
            }
        }

        .yith-wcwl-add-button a,
        .yith-wcwl-wishlistaddedbrowse a,
        .yith-wcwl-wishlistexistsbrowse a {
            font-size: 13px;
            font-weight: 400;

            &:after {
                float: left;
                padding: 0 6px 0 14px;
            }
        }

        .mkd-pli-inner {
            width: 34%;

            .mkd-pli-image {
                padding-top: 0;
            }
        }

        .compare-button {
            width: auto;

            .compare {
                font-size: 13px;
                font-weight: 400;
                color: $additional-text-color;

                &:after {
                    content: '';
                }

                &:before {
                    content: "\e040";
                    font-family: dripicons-v2 !important;
                    color: #adaeb0;
                    font-size: 16px;
                    padding: 0;
                    vertical-align: middle;
                    padding-right: 2px;
                }

                &:hover {
                    color: $first-main-color;
                    &:before {
                        color: $first-main-color;
                    }
                }
            }
        }

        .mkd-pli-inner,
        .mkd-pli-text-wrapper,
        .mkd-pli-cart-wrapper {
            box-sizing: border-box;
            display: table-cell;
            vertical-align: middle;
        }

        .mkd-pli-text-wrapper {
            padding-left: 25px;
            width: 48%;
        }

        .mkd-pli-cart-wrapper {
            text-align: center;
            width: 18%;
            margin: 23px 0 10px;
        }

        &:after {
            width: 0;
        }

    }

    //list layout end

    .mkd-pli {
        @include mkdRelativeHolderLayout();
        box-sizing: border-box;

        .mkd-pli-title {
            margin: 7px 0 4px;
        }

        .mkd-pli-category {
            margin: 11px 0 3px;
        }

        .mkd-pli-excerpt {
            color: $default-heading-color;
            line-height: 1.3em;
            margin: 11px 0 24px;

            ul li {
                font-weight: 400;
                font-size: 13px;
                color: $additional-text-color;
                padding: 2px 0;
            }
        }

        .mkd-pli-sku {
            margin-bottom: 21px;
            display: inline-block;
        }

        .mkd-pli-rating-holder {
            @include mkdAbsoluteHolderLayout();
            line-height: 1;
            margin: 10px 0 3px;
        }

        .mkd-pli-rating {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-family: "ElegantIcons";
            font-size: 12px;
            line-height: 1;
            letter-spacing: 5px;
            color: #d2d2d2;
            overflow: hidden;
            -webkit-font-smoothing: antialiased;

            &:before {
                content: "\e033\e033\e033\e033\e033";
            }

            span {
                position: absolute;
                top: 0;
                left: 0;
                overflow: hidden;

                &:before {
                    content: "\e033\e033\e033\e033\e033";
                    color: #f8e900;
                }
            }
        }

        .mkd-pli-price {
            line-height: 1;
            margin: 14px 0 0;
            font-size: 18px;
            font-weight: 500;
            color: $default-heading-color;

            * {
                font-size: inherit;
                line-height: inherit;
            }

            del {
                margin: 0 8px 0 0;
                font-size: 14px;
                color: #d2d2d2;
                vertical-align: middle;
                display: inline-block;
            }

            ins {
                color: #ff4b4b;
                text-decoration: none;
                vertical-align: middle;
                display: inline-block;
            }

            &.mkd-value-deal {
                ins {
                    color: $value-deal-color;
                }

                del {
                    display: none;
                }
            }

            .mkd-value-deal-label {
                font-size: 12px;
                font-weight: 500;
                color: #d2d2d2;
            }
        }

        &.mkd-pli-list-layout {
            width: 100% !important; //!important rule in order to override column option for this layout type
        }

        .mkd-pli-add-to-cart {

            .added_to_cart {
                font-size: 0;
                border-radius: 50%;
                padding: 0;
                width: 43px;
                height: 43px;
                position: absolute;
                bottom:0;
                right:0;

                &:after {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    content: "\53";
                    font-family: "dripicons-v2" !important;
                    font-size: 18px;
                }
            }

            .add_to_cart_button, .button {
                font-size: 0;
                border-radius: 50%;
                padding: 0;
                width: 43px;
                height: 43px;
                position: relative;
                background-color: #f9f9f9;

                &:before {
                    content: "\6b";
                    font-family: "dripicons-v2" !important;
                    position: absolute;
                    top: 0;
                    left: 47%;
                    color: #fff;
                    font-size: 9px;
                    text-transform: none !important;
                    opacity: 0;
                    pointer-events:none;
                    @include mkdTransform(translate3d(-47%, -100%, 0));
                    -webkit-transition: transform .35s cubic-bezier(.53, .01, .18, 1), opacity .3s ease;
                    -moz-transition: transform .35s cubic-bezier(.53, .01, .18, 1), opacity .3s ease;
                    transition: transform .35s cubic-bezier(.53, .01, .18, 1), opacity .3s ease;
                }

                &:after {
                    position: absolute;
                    top: 50%;
                    left: 47%;
                    @include mkdTransform(translate3d(-47%, -50%, 0));
                    content: "\51";
                    font-family: "dripicons-v2" !important;
                    font-size: 18px;
                    color: #8e8e8e;
                    pointer-events:none;
                    -webkit-transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                    -moz-transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                    transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                }

                &:hover {

                    &:before {
                        @include mkdTransform(translate3d(-30%, 0,0));
                        opacity: 1;
                        color: #fff;
                    }

                    &:after {
                        color: #fff;
                        @include mkdTransform(translate3d(-50%, -40%, 0));
                        -webkit-transition: -webkit-transform .35s cubic-bezier(.53, .01, .18, 1);
                        transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                    }

                }

                &.loading {
                    &:before {
                        @include mkdAnimation(mkdAddingToCart .5s infinite ease-in-out);
                    }
                }

                &.product_type_external {
                    &:before {
                        content: "";
                    }

                    &:after {
                        content: "\6d";
                        left: 48%;
                        text-transform: none;
                        @include mkdTransform(translate(-48%, -50%));
                    }

                    &:hover {
                        &:after {
                            @include mkdTransform(translate(-40%,-50%));
                        }
                    }
                }
            }
        }


        .mkd-pli-category {
            margin: 0;
            display: inline-block;
            vertical-align: middle;

            a {
                color: $additional-text-color;
                font-size: 13px;
                font-weight: 400;

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .mkd-compare-wishlist-wrapper {
            float: right;

            .yith-wcwl-add-button a:after,
            .yith-wcwl-wishlistaddedbrowse a:after,
            .yith-wcwl-wishlistexistsbrowse a:after {
                padding: 0 0 0 10px;
            }

            .compare-button {
                width: auto;
                display: inline-block;
            }
        }
    }

    .mkd-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        .touch & {
            cursor: pointer;
        }

        .mkd-pli-image {
            @include mkdRelativeHolderLayout();
            padding-top: 35px;

            .mkd-labels-holder {
                position: absolute;
                display: block;
                top: 0;
                width: auto;

                .mkd-pli-new-product,
                .mkd-pli-onsale {
                    position: relative;
                    display: inline-block;
                }

                .mkd-pli-onsale {
                    background: transparent;
                }
            }

            img {
                display: block;
                width: 100%;
            }

            .mkd-pli-onsale,
            .mkd-pli-out-of-stock,
            .mkd-pli-new-product {
                position: absolute;
                display: block;
                top: 0;
                width: auto;
                height: 24px;
                color: #ff4b4b;
                font-size: 14px;
                font-weight: 500;
                line-height: 24px;
                letter-spacing: 0;
                text-transform: capitalize;
                padding: 0;
                margin: 0;
                text-align: center;
                z-index: 100;
                @include mkdTransform(translateZ(0));
                box-sizing: border-box;
            }

            .mkd-pli-onsale {
                left: 0;
                background-color: rgba($default-heading-color, .5);
            }

            .mkd-pli-out-of-stock {
                right: 0;
                background-color: rgba($default-heading-color, .5);
            }

        }

        .mkd-pli-link {
            @include mkdAbsoluteHolderLayout();
        }

        &:hover {
            + .mkd-pli-text-wrapper h5 > a {
                color: $first-main-color;
            }
        }
    }

    .mkd-pli-text-wrapper {
        @include mkdRelativeHolderLayout();
        margin: 22px 0 10px;
        text-align: left;
        display: block;
        height: 100%;
        padding-top: 26px;

        .mkd-pli-text-left-holder {
            display: block;
            position: relative;

            .mkd-pli-title-price-holder {
                display: block;
                padding-right: 43px;
                width: 100%;
                box-sizing: border-box;
            }

            ul li {
                font-weight: 400;
                font-size: 13px;
                color: $additional-text-color;
                padding: 2px 0;
            }

        }
    }

    .mkd-pli-add-to-cart {
        position: absolute;
        bottom: 0;
        right: 0;
        overflow: hidden;

        .button,
        .added_to_cart {
            @include mkdButtonDefaultStyle();
            @include mkdButtonSolidColor();
            @include mkdButtonMediumParams();
            @include mkdTransition(opacity .3s ease-in-out);

            &:hover {
                @include mkdButtonSolidHoverColor();
            }

            &.added {
                z-index:1;
                opacity:0;
            }
        }
    }

    &.mkd-product-info-light {

        .mkd-pli-inner {

            .mkd-pli-text-inner {

                .mkd-pli-title,
                .mkd-pli-category a,
                .mkd-pli-excerpt,
                .mkd-pli-rating,
                .mkd-pli-price {
                    color: #fff;
                }

                .mkd-pli-category a:hover {
                    color: rgba(#fff, .8);
                }
            }
        }
    }

    &.mkd-product-info-dark {

        .mkd-pli-inner {

            .mkd-pli-text-inner {

                .mkd-pli-title,
                .mkd-pli-category a,
                .mkd-pli-excerpt,
                .mkd-pli-rating,
                .mkd-pli-price {
                    color: $default-heading-color;
                }

                .mkd-pli-category a:hover {
                    color: rgba($default-heading-color, .8);
                }
            }
        }
    }

    /*************** Standard Type - begin ***************/

    &.mkd-standard-type {

        .mkd-pli {
            float: left;
            @include mkdTransition(opacity .2s ease-in-out 0s);

            &.mkd-pli-list-layout:first-child {
                padding-top: 0;
            }
        }

        .mkd-pl-outer-holder {
            position:relative;
        }

        &.mkd-normal-space {
            margin: 0 0 -30px;
        }

        &.mkd-small-space {
            margin: 0 0 -20px;
        }

        &.mkd-tiny-space {
            margin: 0 0 -10px;
        }

        .mkd-rotate-circles { 
            position:absolute;
            top:200px;
            left:50%;
            width:80px;
            opacity:0;
            @include mkdTransition(opacity .2s ease-in-out 0s);
            @include mkdTransform(translate3d(-50%,-50%,0));

            > div {
                width: 18px;
                height: 18px;
                background-color: $first-main-color;
            }
        }

        &.mkd-pl-loading {
            .mkd-pli { 
                opacity:0;
                @include mkdTransition(opacity .2s ease-in-out 0s);
            }

            .mkd-rotate-circles { 
                opacity:1;
            }
        }

        $standard_list_columns: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($standard_list_columns) {
            &.mkd-#{nth($standard_list_columns,$i+1)}-columns {
                $column_width: 100%/($i+2);

                .mkd-pli {
                    width: $column_width;

                    @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                        &:nth-child(#{$i+2}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }

        @for $i from 0 to length($standard_list_columns) {
            &.mkd-#{nth($standard_list_columns,$i+1)}-columns {

                .mkd-pli {

                    @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                        &:nth-child(#{$i+2}n) {

                            &:after {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }

    /*************** Standard Type - end ***************/

    /*************** Masonry Type - begin ***************/

    &.mkd-masonry-type {

        .mkd-pl-outer {
            opacity: 0;
        }

        .mkd-pl-sizer {
            width: 100%;
        }

        .mkd-pl-gutter {
            width: 0;
        }

        $masonry_list_columns: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($masonry_list_columns) {
            &.mkd-#{nth($masonry_list_columns,$i+1)}-columns {
                $column_width: 100%/($i+2);

                .mkd-pli,
                .mkd-pl-sizer {
                    width: $column_width;
                }

                .mkd-pli.mkd-woo-image-large-width {
                    width: $column_width*2;
                }

                .mkd-pli {

                    @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                        &:nth-child(#{$i+2}n+2) {

                            &:after {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }

    /*************** Masonry Type - end ***************/
}

/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */