/* ==========================================================================
   YITH Compare styles - begin
   ========================================================================== */

.yith-woocompare-widget {
    display: inline-block;
    background-color: transparent;
    margin: 0;
    vertical-align: middle;

    h2,
    ul,
    .clear-all {
        display: none;
    }

    .compare.button {
        font-size: 0;
        background-color: transparent;
        padding: 0;

        &:hover {
            background-color: transparent;

            &:after {
                color: $first-main-color;
            }
        }

        &:after {
            content: "\e040";
            color: $widget-text-color;
            font-family: "dripicons-v2" !important;
            font-size: 24px;
        }
    }
}

.compare-button .compare,
.compare-button .compare.button {
    font-size: 0;
    background-color: transparent;
    padding: 0;

    &:hover {
        background-color: transparent;

        &:after {
            color: $first-main-color;
        }
    }

    &:after {
        content: "\e040";
        color: $additional-text-color;
        font-family: "dripicons-v2" !important;
        font-size: 16px;
        padding: 0;
    }
}

.mkd-light-header {
    .yith-woocompare-widget {
        .compare.button {

            &:hover {

                &:after {
                    opacity: 0.8;
                }
            }

            &:after {
                color: $header-light-color;
            }
        }
    }
}

.mkd-logo-area-light-header {
    .widget.yith-woocompare-widget {
        .compare.button {

            &:hover {

                &:after {
                    opacity: 0.8;
                }
            }

            &:after {
                color: $header-light-color;
            }
        }
    }
}

.mkd-dark-header {
    .yith-woocompare-widget {
        .compare.button {
            color: $header-dark-color;

            &:hover {

                &:after {
                    opacity: 0.8;
                }
            }

            &:after {
                color: $header-dark-color;
            }
        }
    }
}

.mkd-logo-area-dark-header {
    .widget.yith-woocompare-widget {
        .compare.button {
            color: $header-dark-color;

            &:hover {

                &:after {
                    opacity: 0.8;
                }
            }

            &:after {
                color: $header-dark-color;
            }
        }
    }
}

.compare-button .compare.added {
    &:after {
        color: $first-main-color;
    }
}

//enable scroll on touch devices
.touch .mkd-popup-compare #cboxLoadedContent {
    -webkit-overflow-scrolling: touch !important;
    overflow-y: scroll !important;
}

.mkd-popup-compare {

    .yith_woocompare_colorbox {
        #cboxTopLeft,
        #cboxTopCenter,
        #cboxTopRight,
        #cboxMiddleLeft,
        #cboxMiddleRight,
        #cboxBottomLeft,
        #cboxBottomRight {
            width: 0;
            height: 0;
        }

        #cboxBottomCenter {
            background: transparent;
        }

    }

    .dataTables_wrapper .description td ul {
        list-style-type: none;
    }

    #DataTables_Table_0_wrapper, .compare-list {
        display: none;
    }

    #cboxOverlay {
        background-color: rgba(17, 17, 17, .7);
        height: 100vh;
        width: 100vw;
    }

    #colorbox {
        width: 55% !important;
        height: 85% !important;
        left: 50% !important;
        transform: translateX(-50%) !important;

        #cboxWrapper,
        #cboxContent,
        #cboxLoadedContent {
            width: 100% !important;
        }

        #cboxClose {
            background: url(../img/icon-close-popup.png);
            top: 25px;
            right: 41px;
        }
    }

    > h1 {
        position: fixed;
        width: 100%;
        padding: 25px 42px 31px;
        box-sizing: border-box;
        background-color: $first-main-color;
        text-transform: capitalize;
        border-bottom: none;
        font-family: $default-text-font;
        font-size: 22px;
        font-weight: 300;
        z-index: 999;

        a.close {
            font-size: 0;
            &:after {
                content: "\39";
                font-family: "dripicons-v2" !important;
                position: absolute;
                top: 50%;
                right: 10%;
                @include mkdTransform(translateY(-50%));
                font-size: 20px;
            }
        }
    }

    .mkd-pl-holder {

        .mkd-pl-outer-popup {
            overflow: hidden;
            margin: 28px 17px;

            .mkd-item-space {
                margin-top: 75px;

                &:nth-child(n+4) {
                    margin-top: 0;
                }

            }
        }

        .mkd-pli-popup {
            position: relative;
            float: left;
            vertical-align: top;
            width: 33.3333%;
            z-index: 100;
            box-sizing: border-box;

            &:after {
                content: '';
                position: absolute;
                left: 100%;
                top: 0;
                height: 100%;
                width: 1px;
                background-color: $default-border-color;
            }

            .mkd-pli-holder-popup {
                font-family: $default-text-font;
                padding: 10px 33px;

                .mkd-pli-category-popup {
                    margin: 0;
                    display: inline-block;
                    vertical-align: middle;

                    a {
                        color: $additional-text-color;
                        font-weight: 300;
                        text-decoration: none;

                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }

                .mkd-pli-inner {

                    .mkd-pli-image-popup {
                        position: relative;
                        display: inline-block;
                        width: 100%;
                        vertical-align: middle;
                        padding-top: 15px;

                        img {
                            display: block;
                            width: 100%;
                            height: auto;
                        }

                        .mkd-labels-holder {
                            position: absolute;
                            display: block;
                            top: 0;
                            width: auto;

                            .mkd-pl-new-product {
                                font-weight: 500;
                                color: #ff4b4b;
                            }
                        }

                    }
                }

                .mkd-pli-text-wrapper-popup {
                    position: relative;
                    width: 100%;
                    vertical-align: middle;
                    text-align: left;
                    display: table;
                    height: 100%;
                    margin: 22px 0 10px;
                    padding-top: 26px;

                    .mkd-pli-text-left-holder-popup {
                        display: inline-block;

                        .mkd-pli-excerpt-popup {
                            margin-top: 15px;
                            font-weight: 300;
                            color: $additional-text-color;

                            ul {
                                margin-left: 16px;

                                li {
                                    padding: 2px 0;
                                    font-weight: 300;
                                }
                            }
                        }

                        .mkd-pli-sku-popup {
                            display: inline-block;
                            margin: 20px 0;
                            color: $additional-text-color;
                        }
                    }
                }

                .mkd-pli-rating-holder {
                    position: absolute;
                    display: block;
                    width: 100%;
                    top: 0;
                    left: 0;
                    line-height: 1;
                    margin: 10px 0 3px;

                    .mkd-pli-rating {
                        position: relative;
                        display: inline-block;
                        vertical-align: middle;
                        font-family: ElegantIcons;
                        font-size: 12px;
                        line-height: 1;
                        letter-spacing: 5px;
                        color: #d2d2d2;
                        overflow: hidden;
                        -webkit-font-smoothing: antialiased;

                        &:before {
                            content: "\e033\e033\e033\e033\e033";
                        }

                        span {
                            position: absolute;
                            top: 0;
                            left: 0;
                            overflow: hidden;

                            &:before {
                                content: "\e033\e033\e033\e033\e033";
                                color: #f8e900;
                            }
                        }
                    }
                }

                .mkd-pli-title {
                    margin: 0;
                    padding-right: 20px;
                    min-height: 4em;

                    a {
                        font-size: 14px;
                        text-decoration: none;
                        color: #000;

                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }

                .mkd-pli-price {
                    line-height: 1;
                    margin: 14px 0 0;
                    font-size: 18px;
                    font-weight: 500;
                    color: $default-heading-color;

                    ins {
                        color: #ff4b4b;
                        text-decoration: none;
                        vertical-align: middle;
                        display: inline-block;
                    }

                    del {
                        margin: 0 8px 0 0;
                        font-size: 14px;
                        color: #d2d2d2;
                        vertical-align: middle;
                        display: inline-block;
                    }
                }

                .mkd-pli-add-to-cart {
                    vertical-align: bottom;
                    display: table-cell;
                    text-align: right;
                    bottom: -3px;

                    .add_to_cart_button, .product_type_external {
                        font-size: 0;
                        border-radius: 50%;
                        padding: 0;
                        width: 43px;
                        height: 43px;
                        position: relative;
                        background-color: #f9f9f9;

                        &:before {
                            content: "\6b";
                            font-family: "dripicons-v2" !important;
                            position: absolute;
                            top: 0;
                            left: 47%;
                            color: #fff;
                            font-size: 9px;
                            text-transform: none !important;
                            opacity: 0;
                            pointer-events:none;
                            @include mkdTransform(translate(-47%, -100%));
                            -webkit-transition: transform .35s cubic-bezier(.53, .01, .18, 1), opacity .3s ease;
                            -moz-transition: transform .35s cubic-bezier(.53, .01, .18, 1), opacity .3s ease;
                            transition: transform .35s cubic-bezier(.53, .01, .18, 1), opacity .3s ease;
                        }

                        &:after {
                            position: absolute;
                            top: 50%;
                            left: 47%;
                            @include mkdTransform(translate(-47%, -50%));
                            content: "\51";
                            font-family: "dripicons-v2" !important;
                            font-size: 18px;
                            color: #8e8e8e;
                            pointer-events:none;
                            -webkit-transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                            -moz-transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                            transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                        }

                        &.loading {
                            &:before {
                                @include mkdAnimation(mkdAddingToCart .5s infinite ease-in-out);
                            }
                        }

                        &:hover {
                            color: #fff;
                            background-color: $first-main-color;

                            &:before {
                                @include mkdTransform(translate(-30%, 0));
                                opacity: 1;
                                color: #fff;
                            }

                            &:after {
                                color: #fff;
                                @include mkdTransform(translate(-50%, -40%));
                                -webkit-transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                                -moz-transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                                transition: transform .35s cubic-bezier(.53, .01, .18, 1);
                            }
                        }
                    }

                    .added_to_cart {
                        font-size: 0;
                        border-radius: 50%;
                        padding: 0;
                        width: 43px;
                        height: 43px;
                        position: absolute;
                        bottom:0;
                        right:0;

                        &:after {
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                            content: "\53";
                            font-family: dripicons-v2 !important;
                            font-size: 18px;
                        }
                    }
                }

                .remove {

                    .remove-popup-inner {
                        text-align: center;
                        box-sizing: border-box;
                        margin-top: 43px;

                        span {
                            vertical-align: middle;
                            padding: 4px;
                            font-size: 17px;
                            color: #ff4b4b;
                            @include mkdTransition(all .2s ease-in-out);
                        }

                        a {
                            display: block;
                            position: relative;
                            box-sizing: border-box;
                            background-color: #fafafa;
                            padding: 17px;
                            font-size: 12px;
                            line-height: 1.5em;
                            font-weight: 500;
                            color: $default-heading-color;
                            text-decoration: none;

                            @include mkdTransition(all .2s ease-in-out);

                            &:hover {
                                background-color: $first-main-color;
                                color: #fff;

                                span {
                                    color: #fff;
                                }
                            }
                        }

                    }
                }

            }
        }

        .mkd-pli-out-of-stock-holder {
            .mkd-pli-add-to-cart {
                vertical-align: bottom;
                display: table-cell;
                text-align: right;
                bottom: -3px;
                .button {
                    font-size: 0;
                    border-radius: 50%;
                    padding: 0;
                    width: 43px;
                    height: 43px;
                    position: relative;
                    background-color: #f9f9f9;

                    &:before {
                        content: "";
                    }

                    &:after {
                        color: #ff4b4b;
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        content: "\e067";
                        font-family: "dripicons-v2" !important;
                        font-size: 18px;

                    }
                    &:hover {
                        background-color: #f9f9f9;
                    }
                }
            }
            .mkd-pli-inner {
                .mkd-pli-image-popup {
                    position: relative;
                    display: inline-block;
                    width: 100%;
                    vertical-align: middle;
                    padding-top: 35px;

                    .mkd-pl-out-of-stock {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        vertical-align: middle;
                        transform: translate(-50%, -50%);
                        right: auto;
                        background: 0 0;
                        font-size: 14px;
                        font-weight: 500;
                        color: #ff4b4b;
                    }
                    img {
                        opacity: .1;
                    }
                }
            }
        }

        &.mkd-three-columns {

            .mkd-pli-popup {

                @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                    &:nth-child(3n+1) {
                        clear: both;
                    }
                }
            }
        }

    }

}

/* ==========================================================================
   YITH Compare styles - end
   ========================================================================== */