/* ==========================================================================
   WooCommerce Message styles - begin
   ========================================================================== */
.mkd-woocommerce-page {

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 17px 30px;
        margin: 0 0 30px;
        line-height: 38px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }

        //clearfix
        &:before, &:after {
            content: " ";
            display: table;
        }

        &:after {
            clear: both;
        }
    }

    .mkd-content {

        .woocommerce-message {
            .wc-forward:not(.added_to_cart):not(.checkout-button) {
                position: absolute;
                right: 30px;
                top: 50%;
                @include mkdTransform(translateY(-50%));
            }
        }
    }

    .woocommerce-info {

        .showcoupon {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Message styles - end
   ========================================================================== */