/* ==========================================================================
   WooCommerce MyAccount page styles - begin
   ========================================================================== */

.mkd-woocommerce-page.woocommerce-account {

	.woocommerce {
		margin: 0 0 45px;
		.u-columns {
			width: 100%;
			box-sizing: border-box;

			.u-column1 {
				border-right: 1px solid $default-border-color;
				padding-right: 7.5%;
			}

			.u-column2 {
				padding-left: 7.5%;
			}

			.u-column1,
			.u-column2 {
				position: relative;
				width: 50%;
				float: left;
				box-sizing: border-box;

				h2 {
					margin-bottom: 15px;
				}
			}
		}
	}

	.woocommerce-MyAccount-navigation,
	.woocommerce-MyAccount-content {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		float: left;
		box-sizing: border-box;
	}

	.woocommerce-MyAccount-navigation {
		width: 30%;
		padding: 0 15px 0 0;

		ul {
			padding: 0 15px 0 0;
			margin: 0;
			list-style: none;
			border-right: 1px solid $default-border-color;

			li {
				margin: 0;
				padding: 0;

				&:not(:last-child) {
					margin: 0 0 18px;
				}

				a {
					font-weight: 500;
				}
			}
		}
	}

	.woocommerce-MyAccount-content {
		width: 70%;
		padding: 0 0 0 30px;

		> p:first-child {
			margin: 5px 0 30px;
		}

		.woocommerce-info {
			.button {
				border-radius: 2em;
				&:hover {
					color: #fff;
				}
			}
		}
	}

	.woocommerce {
		.register {
			p:nth-child(2) {
				margin-top: 19px;
			}
		}

		p {
			margin: 5px 0;
			label {
				font-weight: 400;
				font-size: 12px;
				color: #343434;

				&.woocommerce-form__label-for-checkbox {
					margin-top: 15px;
					vertical-align: middle;
					display: inline-block;
					span {
						font-size: 13px;
						color: #353535;
						text-transform: capitalize;
					}
				}
			}

			input {
				&[type=text],
				&[type=email],
				&[type=password] {
					padding: 15px 20px;
					margin-top: 5px;
					width: 100%;
					&:focus {
						border: 1px solid $first-main-color;
					}
				}

				&[type=submit] {
					border-radius: 50px;
					padding: 12px 49px;
					margin-top: 15px;
				}
			}
		}

		form.login {
			margin: 0 0 34px;

			p.form-row {

				&.mkd-login-form-text {
					margin: 0 0 7px;
				}

				&:nth-child(3) {
					width: 50%;
					display: inline-block;
					vertical-align: middle;
				}

				input[type=submit] {
					margin-top: 10px;
				}
			}

			input[type='checkbox'] {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				top: -1px;
				margin: 0 3px 0 5px;
			}

			.lost_password {
				display: inline-block;
				margin: 28px 0;
				float: right;
				vertical-align: middle;

				a {
					font-weight: 500;
					font-size: 12px;
					color: $first-main-color;
				}
			}
		}

		form.register {
			p {
				input {

					&[type=submit] {
						padding: 12px 40px;
					}
				}
			}
		}

		form.edit-account {
			margin: 0 0 15px;

			p {
				margin: 0;
			}

			label {
				display: block;
				margin: 0 0 5px;
			}

			fieldset {

				> legend {
					display: block;
					color: $default-heading-color;
					margin: 0 0 10px;
				}
			}
		}

		> p {
			display: block;
			margin: 0 0 30px;
		}

		> h2,
		> header h2 {
			display: block;
			margin: 16px 0 25px;
		}

		table.shop_table {
			padding: 0;
			margin: 0 0 35px;

			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
			}

			td, th {
				padding: 14px 0;
				text-align: left;
				vertical-align: middle;
			}

			th {
				border: 0;
			}

			td {

				&.order-actions {
					text-align: right;
				}
			}

			.variation {

				> * {
					display: inline-block;
					vertical-align: middle;
					padding: 0;
					margin: 0;
				}

				p {
					margin: 0;
				}
			}
		}

		.col2-set.addresses {
			@include mkdRelativeHolderLayout();
			clear: both;
			margin: 0 0 15px;

			.col-1,
			.col-2 {
				position: relative;
				display: inline-block;
				width: 49%;
				margin-right: 1%;
				float: left;
				vertical-align: middle;
			}

			.col-2 {
				margin-left: 1%;
				margin-right: 0;
			}

			.title {
				margin: 4px 0 25px;
			}

			h3 {
				margin: 0;
			}

			address {
				font-style: normal;
			}

			.edit {
				position: relative;

				&:before,
				&:after {
					content: "";
					position: absolute;
					width: 100%;
					left: 0;
					bottom: -2px;
					border-bottom: 2px solid #97a8fd;
					opacity: 0;
					@include mkdTransformOrigin(100% 50%);
				}

				&:before {
					@include mkdTransform(scaleX(0));
					@include mkdTransformOrigin(0 50%);
					-webkit-transition: transform 0s .2s;
					-moz-transition: transform 0s .2s;
					transition: transform 0s .2s;
					opacity: 0;
				}

				&:after {
					@include mkdTransform(scaleX(0));
					-webkit-transition: transform .2s,opacity 0s .2s;
					-moz-transition: transform .2s,opacity 0s .2s;
					transition: transform .2s,opacity 0s .2s;
					opacity: 0;

				}

				&:hover {
					color: $first-main-color;
					@include mkdTransition(color .2s ease-in-out);

					&:before {
						@include mkdTransform(scaleX(1));
						opacity: 1;
						@include mkdTransition(all .2s ease-in-out);

					}

					&:after {
						@include mkdTransform(scaleX(1));
						opacity: 1;
						@include mkdTransition(all 0s .2s);
					}
				}
			}
		}
	}

	.vc_row {

		.woocommerce {

			> h2 {
				font-size: 30px;
				line-height: 40px;
				margin: 0 0 25px;
			}

			form.login {
				margin: 0;

				p {
					//@include mkdRelativeHolderLayout();

					label {

						//&:not(.inline) {
						//	display: block;
						//	margin: 14px 0;
						//	color: $default-heading-color;
						//	font-size: 18px;
						//	line-height: 38px;
						//}

						&.inline {
							position: relative;
							display: inline-block;
							vertical-align: middle;
							margin: 10px 0 0;
						}
					}

					input.button {
						margin: 10px 0 0;
					}
				}
			}
		}
	}
}

.mkd-woocommerce-page.woocommerce-account.woocommerce-edit-address {

	.woocommerce {

		> form {
			margin: 0 0 15px;

			> *:first-child {
				margin: 0 0 25px;
			}

			p {
				margin: 0;
			}

			label {
				display: block;
				margin: 0 0 5px;
			}
		}
	}
}

/* ==========================================================================
   WooCommerce MyAccount page styles - end
   ========================================================================== */