/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkd-content,
div.woocommerce {

    .sold-individually .mkd-quantity-buttons {
        display: none;
    }

    .mkd-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        background-color: #fff;
        border: 1px solid $default-border-color;
        box-sizing: border-box;
        line-height: 50px;
        height: 50px;
        padding: 0 10px 0 16px;
        margin: 0px -4px 0 0;

        .mkd-quantity-label,
        .mkd-quantity-input,
        .mkd-quantity-minus,
        .mkd-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: #999;
            background-color: transparent;
            border: 0;
            text-align: center;
            box-sizing: border-box;
            font-size: 16px;
        }

        .mkd-quantity-label{
            margin-right: 35px;
            font-size: 12px;
            color: #908989;
            font-weight: 400;
            height: 22px;
            line-height: 17px;
        }

        .mkd-quantity-input {
            position: relative;
            width: 27px;
            padding: 0;
            margin: 2px 0 0;
            font-family: inherit;
            outline: 0;
            font-weight: 400;
            font-size: 14px;
            bottom: 2px;

        }

        .mkd-quantity-minus,
        .mkd-quantity-plus {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            height: 22px;
            line-height: 1.6em;
            border-left: 0;
            cursor: pointer;
            @include mkdTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }
    }
}

.woocommerce-page .mkd-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        position: relative;
        width: 100%;
        margin: 0 0 14px;
        padding: 16px 24px 15px;
        font-family: inherit;
        font-size: 12px;
        line-height: 18px;
        font-weight: inherit;
        color: $default-text-color;
        background-color: transparent;
        border: 1px solid $default-border-color;
        outline: 0;
        -webkit-appearance: none;
        box-sizing: border-box;

        &:focus {
            border-color: #41c4fd;
        }
    
        @include mkdPlaceholder {
            color: inherit;
        }
    }

    input[type="email"] {
        display: block;
        width: 50%;
    }

    textarea {
        height: 146px;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}

.woocommerce-page .mkd-content {
    .woocommerce-checkout {
        input[type="email"] {
            width: 100%;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */