.mkd-product-search-holder {

    .mkd-product-search-form {
        position: relative;
        border: 1px solid $default-border-color;
        box-sizing: border-box;
        height: 50px;
        min-width: 736px;
        margin-right: 10px;

        .tt-suggestion.tt-selectable {
            cursor:pointer;
        }
    }

    .mkd-product-category-holder {
        position: absolute;
        top: 2px;
        left: 2px;
        z-index: 6;

        &:after {
            content: "\54";
            font-family: "dripicons-v2" !important;
            color: #000;
            font-size: 11px;
            position: absolute;
            right: 22px;
            top: 10px;
            pointer-events: none;
        }
    }

    .mkd-product-category {
        display: block;
        width: 153px;
        height: 44px;
        outline: 0;
        border: 0;
        border-radius: 0;
        padding: 0 40px 0 23px;
        color: #000;
        background-color: $default-border-color;
        font-size: 12px;
        font-family: $default-text-font;
        font-weight: 500;

        //remove arrow
        -webkit-appearance: none;
        -moz-appearance: none;
        text-indent: 1px;

        select::-ms-expand {
            display: none;
        }
    }

    //typeahead style

    .twitter-typeahead {
        width: 100%;
    }

    .tt-menu {
        background-color: #fff;
        top: calc(100% + 6px) !important; //!important rule in order to override script inline css
        box-shadow: 0px 1px 18.8px 1.2px rgba(87, 98, 101, 0.06);
        padding: 16px 0;
    }

    .tt-suggestion {
        width: 50%;
        float: left;
        padding: 17px 32px;
        box-sizing: border-box;

        &:nth-child(2n+1) {
            clear: both;
        }

        strong {
            font-weight: inherit;
            color: inherit;
        }

        .mkd-product-search-item {
            display: table;
            position: relative;

            &:after {
                content: '';
                position: absolute;
                right: -32px;
                top: 0px;
                width: 1px;
                height: 100%;
                background-color: $default-border-color;
            }

            .mkd-product-search-image {
                display: table-cell;
                vertical-align: middle;
                width: 35%;

                img {
                    display: block;
                }
            }

            .mkd-product-search-item-cat {
                text-transform: capitalize;
                font-size: 12px;
                color: $additional-text-color;
                margin-top: -4px;
            }

            .mkd-product-search-item-content {
                display: table-cell;
                vertical-align: middle;
                width: 65%;
                padding: 0 0 0 20px;
                text-align: left;
            }

            .mkd-product-search-title {
                margin-top: 3px;
                margin-bottom: 5px;

                strong {
                    font-weight: inherit;
                    color: $first-main-color;
                }
            }

            .mkd-product-search-price {
                line-height: 1;
                margin: 11px 0 0;
                font-size: 14px;
                font-weight: 500;
                color: $default-heading-color;
                display: block;

                * {
                    font-size: inherit;
                    line-height: inherit;
                }

                del {
                    margin: 0 8px 0 0;
                    font-size: 12px;
                    color: #d2d2d2;
                    vertical-align: middle;
                    display: inline-block;
                }

                ins {
                    color: #ff4b4b;
                    text-decoration: none;
                    vertical-align: middle;
                    display: inline-block;
                }
            }
        }

        &:nth-child(2n), &:last-child {
            .mkd-product-search-item:after {
                display: none;
            }
        }
    }

    .mkd-product-search {
        margin: 0;
        padding: 14px 72px 14px 177px;
        border: 0;
        background-color: #fff !important; //because of the inline css

        @include mkdPlaceholder {
            color: #908989;
            font-weight: 400;
            font-size: 12px;
        }
    }

    .mkd-product-search-submit {
        display: block;
        position: absolute;
        top: -1px;
        right: -1px;
        width: 50px;
        height: 50px;
        color: #fff;
        background-color: $first-main-color;
        border: 0;
        outline: 0;
        font-size: 20px;
        cursor: pointer;

        i {
            position: relative;
            top: 2px;
        }
    }
}

.mkd-logo-area-light-header,
.mkd-light-header {
    .mkd-product-search-holder {
        .mkd-product-search {
            background-color: #fff !important;
        }

        .mkd-product-search-submit {
            background-color: #ffd740;
        }
    }
}

.mkd-logo-area-dark-header {
    .mkd-product-search-holder {
        .mkd-product-search {
            background-color: transparent !important;
        }

        .mkd-product-search-submit {
            background-color: $first-main-color;
        }
    }
}