/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */

.mkd-woocommerce-page {

	&.woocommerce-cart {

		.woocommerce > form {
			float: left;
			margin-bottom: 90px;
			width: 65%;

			> h3 {
				line-height: 1.1em;
				border-bottom: 2px solid $default-heading-color;
				display: inline-block;
			}
		}

		.cart-collaterals {
			float: right;
			margin-bottom: 100px;
			width: 30%;
			padding: 30px 35px;
			box-sizing: border-box;
			border: 1px solid $default-border-color;
		}
	}
	
	table.cart {
		
		thead {
			display: none;
		}
		
		tbody {
			
			tr {
				border: 0;
			}
		}
		
		tr.cart_item {
			border-bottom: 1px solid $default-border-color;

			.product-name {
				font-size: 14px;
				font-weight: 500;
				letter-spacing: 0;
				width: 45%;
				padding-right: 50px;
			}

			.product-subtotal,
			.product-price {
				font-size: 18px;
				font-weight: 500;
				color: $default-heading-color;
				width: 9%;
			}

			.product-subtotal {
				text-align: right;
			}

			.product-price {
				text-align: left;
			}

			.product-quantity {
				width: 22%;
			}


			.product-price del {
				text-decoration: none;

				span {
					font-size: 14px;
					color: #d2d2d2;
				}
			}

			.product-price ins {
				text-decoration: none;

				span {
					color: #ff4b4b;
				}
			}
			
			td {
				padding: 53px 0;
				vertical-align: middle;
				text-align: inherit;
				
				&.product-remove {
					padding-left: 10px;
					padding-right: 10px;
					
					a {
						color: $default-text-color;
						font-size: 0;
						line-height: 1;
						
						&:after {
							content: "\39";
							display: inline-block;
							vertical-align: middle;
							speak: none;
							color: $default-heading-color;
							font-family: "dripicons-v2" !important;
							font-size: 22px;
							line-height: inherit;
							font-style: normal;
							font-weight: normal;
							font-variant: normal;
							text-transform: none;
							text-rendering: auto;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
						
						&:hover {
							&:after {
								color: $first-main-color;
							}
						}
					}
				}
				
				&.product-thumbnail {
					width: 86px;
					padding-right: 10px;
					
					a, img {
						display: block;
					}
				}
				
				.variation {
					
					> * {
						display: inline-block;
						vertical-align: middle;
						padding: 0;
						margin: 0;
					}
					
					p {
						margin: 0;
					}
				}
			}
		}

		td.actions {

			padding: 0;
			border-bottom: 1px solid $default-border-color;

			.coupon {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				float: left;
				background-color: #fafafa;
				box-sizing: border-box;
				padding-right: 10px;
				width: 50%;
				text-align: left;

				label {
					display: inline-block;
					vertical-align: middle;
					font-size: 0;

					&:after {
						content: "\50";
						position: relative;
						display: inline-block;
						vertical-align: top;
						font-family: "dripicons-v2" !important;
						font-size: 16px;
						-webkit-font-smoothing: antialiased;
						-moz-osx-font-smoothing: grayscale;
						width: 50px;
						height: 50px;
						line-height: 50px;
						text-align: center;
						color: $default-heading-color;
					}
				}

				input[type="text"] {
					border: 0;
					width: calc(100% - 175px);
					margin: 0 0 0 -3px;
					font-size: 13px;
					padding: 0;
					background-color: transparent;
				}

				input[type="submit"]{
					float: right;
				}
			}

			input[type="submit"],
			button[type="submit"]{
				background-color: transparent;
				border: 0;
				padding: 0;
				margin: 0;
				font-size: 12px;
				font-weight: 600;
				text-transform: uppercase;
				letter-spacing: 2px;
				font-family: $default-text-font;
				color: $default-heading-color;
				cursor: pointer;
				line-height: 50px;
				height: 50px;
			}


			button[name="apply_coupon"],
			button[name="update_cart"]{
				font-weight: 500;
				font-size: 12px;
				color: $first-main-color;
				text-transform: capitalize;
				letter-spacing: 0;
				&:hover {
					background-color: transparent;
				}
			}

			input[name="coupon_code"] {
				font-weight: 400;
				font-size: 13px;
				color: $default-heading-color;
				letter-spacing: 0;
			}


			> input {
				float: right;
			}
		}

	}

	.mkd-cart-go-back{
		display: inline-block;
		position: relative;
		font-size: 12px;
		line-height: 22px;
		color: $first-main-color;
		font-weight: 500;
		margin-top: 24px;

		&:before {
			content: "\55";
			position: relative;
			display: inline-block;
			vertical-align: top;
			font-family: "dripicons-v2" !important;
			font-size: 14px;
			line-height: inherit;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
			margin-right: 2px;

		}
	}
	
	.cart-collaterals {
		@include mkdRelativeHolderLayout;
		margin: 8px 0 50px;
		
		h2 {
			position: relative;
			font-size: 18px;
			margin: 0 0 32px;
			display: inline-block;
			border-bottom: 2px solid #000;
			line-height: 1.1em;
			font-weight: 500;
		}
		
		table {
			margin: 0 0 8px;
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;

				&.order-total {
					border: 0;

					th {
						font-size: 18px;
						padding: 26px 0;
					}

					td {
						text-align: right;
					}
				}

				&.cart-subtotal {
					border: 0;
				}
			}
			
			td, th {
				padding: 13px 0;
				text-align: left;
				vertical-align: middle;
			}

			td {
				font-weight: 500;
				font-size: 18px;
				color: $default-heading-color;
			}

			td[data-title="Shipping"] {
				padding: 7px 0 59px;
			}
			
			th {
				width: 20%;
				padding-right: 39px;
				border: 0;
				font-weight: 500;
				font-size: 16px;
				color: $default-heading-color;
			}

			.shipping th {
				vertical-align: top;
			}
		}
		
		tr.shipping {
			
			#shipping_method {
				list-style: none;
				margin: 0 0 23px;
				
				li {
					margin: 3px 0;
					padding: 0;

					label {
						color: $additional-text-color;
						font-weight: 300;
						font-size: 13px;
					}
					
					> * {
						display: inline-block;
						vertical-align: middle;
						margin: 0;
					}
				}
			}
		}
		
		.woocommerce-shipping-calculator {
			
			.shipping-calculator-button {
				display: block;
				margin: 0 0 31px;

				&:after {
					content: "\54";
					position: relative;
					display: inline-block;
					vertical-align: top;
					font-family: "dripicons-v2" !important;
					line-height: inherit;
					color: #454545;
					font-size: 14px;
					margin-left: 18px;
				}
			}

			input[type=text] {
				padding: 15px 14px;
				margin-bottom: 10px;
			}
			
			p {
				margin: 0;
				font-size: 16px;
			}

			.shipping-calculator-form {
				button[name="calc_shipping"] {
					color: $default-heading-color;
					background-color: #e0e1e9;
					width: 100%;
					padding: 13px 31px;
					font-size: 14px;
					font-weight: 500;
					&:hover {
						color: #fff;
					}
				}
			}
		}
	}
	
	.cross-sells {
		margin: 30px 0 0;
		border-top: 1px solid $default-border-color;
		
		> h2 {
			display: block;
			margin: 36px 0 50px;
			text-align: center;
		}
	}

	.cart-collaterals {

		.cart_totals {
			margin: 1px 0 20px;

			.select2-container--default .select2-selection--single .select2-selection__rendered {
				font-weight: 400;
				color: #908989;
				font-size: 13px;
			}

			.shipping-calculator-form {
				input[type=text] {
					font-weight: 400;
					color: #908989;
				}

				.select2-container--default {
					width: 100% !important;
					margin: 0 0 7px;
				}
			}

			.wc-proceed-to-checkout a {
				width: 100%;
				text-align: center;
				padding: 18px 31px;
				font-size: 14px;
			}
		}
	}
}

.mkd-woocommerce-page {
	
	.cart-empty {
		position: relative;
		display: block;
		font-size: 25px;
		line-height: 1.5em;
		text-align: center;
		color: $default-heading-color;
		margin: 40px 0 33px;
	}
	
	.return-to-shop {
		position: relative;
		display: block;
		text-align: center;
		margin: 0 0 140px;
	}
}
/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */