/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.mkd-woo-pagination-holder {
    @include mkdRelativeHolderLayout();
    margin: 0 0 66px;
    clear: both;

    .mkd-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 100%;
    margin: 0;
    padding: 0;
    text-align: center;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        margin: 0;
        padding: 0;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0;

            a,
            span {
                position: relative;
                display: inline-block;
	            width: auto;
                margin: 0 5px;
                padding: 0;
                vertical-align: top;
                text-decoration: none;
                font-size: 18px;
                font-weight: 400;
                cursor: pointer;
                white-space: nowrap;
                color: $additional-text-color;
                @include mkdTransition(color .2s ease-out);

                &:hover,
                &.current {
                    color: $first-main-color;
                }
            }

            a {

                &.next,
                &.prev {
                    font-size: 0;
                    line-height: 1;
                    margin: 0 6px 0 4px;

                    &:before {
                        position: relative;
                        top: -1px;
                        display: block;
                        font-family: 'dripicons-v2';
                        font-size: 17px;
                        line-height: 1.5em;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }
    
                &.prev {
        
                    &:before {
                        content: "\55";
                    }
                }
    
                &.next {
        
                    &:before {
                        content: "\56";
                    }
                }
            }
        }
    }
}

