/* ==========================================================================
   Product List - Slider shortcode styles - begin
   ========================================================================== */

.mkd-plsl-holder {
    @include mkdRelativeHolderLayout();

    .mkd-plsl-outer {
        @include mkdRelativeHolderLayout();

        .mkd-plsli {
            position: relative;
            display: inline-block;
            vertical-align: top;
            width: 100%;
            padding: 0;
            z-index: 100;
            box-sizing: border-box;

            .mkd-plsli-holder {
                padding: 20px;
                background-color: #fff;
            }

            .mkd-plsli-title {
                margin: 7px 0 4px;
            }

            .mkd-plsli-category {
                margin: 11px 0 3px;
            }

            .mkd-plsli-excerpt {
                color: $default-heading-color;
                line-height: 1.3em;
                margin: 11px 0 24px;
            }

            .mkd-plsli-sku {
                margin-bottom: 21px;
                display: inline-block;
            }

            .mkd-plsli-rating-holder {
                @include mkdRelativeHolderLayout();
                line-height: 1;
                margin: 10px 0 3px;
            }

            .mkd-plsli-rating {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: "ElegantIcons";
                font-size: 12px;
                line-height: 1;
                letter-spacing: 5px;
                color: #d2d2d2;
                overflow: hidden;
                -webkit-font-smoothing: antialiased;

                &:before {
                    content: "\e033\e033\e033\e033\e033";
                }

                span {
                    position: absolute;
                    top: 0;
                    left: 0;
                    overflow: hidden;

                    &:before {
                        content: "\e033\e033\e033\e033\e033";
                        color: #f8e900;
                    }
                }
            }

            .mkd-plsli-price {
                line-height: 1;
                margin: 14px 0 0;
                font-size: 18px;
                font-weight: 500;
                color: $default-heading-color;

                * {
                    font-size: inherit;
                    line-height: inherit;
                }

                del {
                    margin: 0 8px 0 0;
                    font-size: 14px;
                    color: #d2d2d2;
                    vertical-align: middle;
                    display: inline-block;
                }

                ins {
                    color: #ff4b4b;
                    text-decoration: none;
                    vertical-align: middle;
                    display: inline-block;
                }
            }

            .mkd-plsli-inner {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                overflow: hidden;

                .touch & {
                    cursor: pointer;
                }

                .mkd-plsli-image {
                    @include mkdRelativeHolderLayout();
                    padding-top: 35px;

                    .mkd-labels-holder {
                        position: absolute;
                        display: block;
                        top: 0;
                        width: auto;

                        .mkd-plsli-new-product,
                        .mkd-plsli-onsale {
                            position: relative;
                            display: inline-block;
                        }

                        .mkd-plsli-onsale {
                            background: transparent;
                        }
                    }

                    img {
                        display: block;
                        width: 100%;
                    }

                    .mkd-plsli-onsale,
                    .mkd-plsli-out-of-stock,
                    .mkd-plsli-new-product {
                        position: absolute;
                        display: block;
                        top: 0;
                        width: auto;
                        height: 24px;
                        color: #ff4b4b;
                        font-size: 14px;
                        font-weight: 500;
                        line-height: 24px;
                        letter-spacing: 0;
                        text-transform: capitalize;
                        padding: 0;
                        margin: 0;
                        text-align: center;
                        z-index: 100;
                        @include mkdTransform(translateZ(0));
                        box-sizing: border-box;
                    }

                    .mkd-plsli-onsale {
                        left: 0;
                    }

                    .mkd-plsli-out-of-stock {
                        right: 0;
                    }
                }

                &:hover {
                    + .mkd-plsli-text-wrapper .mkd-plsli-title>a {
                        color: $first-main-color;
                    }
                }

                .mkd-plsli-link {
                    @include mkdAbsoluteHolderLayout();
                }
            }

            .mkd-plsli-text-wrapper {
                @include mkdRelativeHolderLayout();
                margin: 22px 0 10px;
                text-align: left;
                display: table;
                height: 100%;

                .mkd-plsli-text-left-holder {
                    display: inline-block;

                    ul li {
                        font-weight: 400;
                        font-size: 13px;
                        color: $additional-text-color;
                        padding: 2px 0;
                    }

                }
            }

            .mkd-plsli-add-to-cart {
                vertical-align: bottom;
                display: table-cell;
                height: 100%;
                text-align: right;

                .button,
                .added_to_cart {
                    @include mkdButtonDefaultStyle();
                    @include mkdButtonSolidColor();
                    @include mkdButtonMediumParams();
                    @include mkdTransition(opacity .3s ease-in-out);

                    &:hover {
                        @include mkdButtonSolidHoverColor();
                    }

                    &.added {
                        z-index:1;
                        opacity:0;
                    }
                }

                .added_to_cart {
                    font-size: 0;
                    border-radius: 50%;
                    padding: 0;
                    width: 43px;
                    height: 43px;
                    position: absolute;
                    bottom:0;
                    right:0;

                    &:after {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        content: "\53";
                        font-family: "dripicons-v2" !important;
                        font-size: 18px;

                    }
                }

                .button, .product_type_external {
                    font-size: 0;
                    border-radius: 50%;
                    padding: 0;
                    width: 43px;
                    height: 43px;
                    position: relative;
                    background-color: #f9f9f9;

                    &:before {
                        content: "\6b";
                        font-family: "dripicons-v2" !important;
                        position: absolute;
                        top: 0;
                        left: 47%;
                        color: #fff;
                        font-size: 9px;
                        text-transform: none !important;
                        opacity: 0;
                        pointer-events:none;
                        @include mkdTransform(translate(-47%, -100%));
                        -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
                        -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
                        transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
                    }

                    &:after {
                        position: absolute;
                        top: 50%;
                        left: 47%;
                        @include mkdTransform(translate(-47%, -50%));
                        content: "\51";
                        font-family: "dripicons-v2" !important;
                        font-size: 18px;
                        color: #8e8e8e;
                        pointer-events:none;
                        -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                        -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                        transition: transform .35s cubic-bezier(.53,.01,.18,1);
                    }

                    &.loading {
                        &:before {
                            @include mkdAnimation(mkdAddingToCart .5s infinite ease-in-out);
                        }
                    }

                    &:hover {

                        &:before {
                            @include mkdTransform(translate(-30%, 0));
                            opacity: 1;
                            color: #fff;
                        }

                        &:after{
                            color: #fff;
                            @include mkdTransform(translate(-50%, -40%));
                            -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                            -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                            transition: transform .35s cubic-bezier(.53,.01,.18,1);
                        }
                    }
                }
            }

            .mkd-plsli-category {
                margin: 0;
                display: inline-block;
                vertical-align: middle;

                a {
                    color: $additional-text-color;
                    font-size: 13px;
                    font-weight: 400;

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }

            .mkd-compare-wishlist-wrapper {
                float: right;

                .compare-button {
                    width: auto;
                    display: inline-block;
                }
            }
        }
    }

    &.mkd-plsl-nav-default-pos {
        .mkd-owl-slider {
            .owl-nav {
                position: absolute;
                width: 100%;
                top: 50%;
                //right: 16px;

                .owl-prev,
                .owl-next {
                    span {
                        font-size: 24px;

                        &:before {
                            @include mkdTransition(all .2s ease-in-out);
                        }
                    }
                    border-radius: 50%;
                    background-color: #ffffff;
                    padding: 10px;
                    border: 1px solid #daddec;

                    &:hover {
                        span:before {
                            color: $first-main-color;
                        }
                    }
                }

                .owl-prev {
                    left: -25px;
                }
                .owl-next {
                    right: -25px;
                    span {
                        left: 2px;
                    }
                }
            }
        }
    }

    &.mkd-plsl-nav-top-pos {

        .mkd-owl-slider {
            .owl-nav {
                position: absolute;
                top: -41px;
                right: 16px;

                .owl-prev,
                .owl-next {
                    span {
                        font-size: 24px;
                    }
                }

                .owl-prev {
                    left: -25px;
                }
                .owl-next {
                    left: 0px;
                }
            }
        }

    }

    &.mkd-plsl-nav-light-skin {

        .owl-nav {

            .owl-prev,
            .owl-next {
                color: $header-light-color;

                &:hover {
                    color: $header-light-hover-color;
                }
            }
        }
    }

    &.mkd-plsl-pag-inside-slider {

        .owl-prev,
        .owl-next {
            @include mkdTransform(translateY(-50%));
        }

        .owl-dots {
            position: absolute;
            left: 0;
            bottom: 10px;
            margin: 0;
        }
    }

    &.mkd-plsl-pag-light-skin {

        .owl-dots {

            .owl-dot {

                span {
                    background-color: $header-light-hover-color;
                }

                &:hover,
                &.active {

                    span {
                        background-color: $header-light-color;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   Product List - Slider shortcode styles - end
   ========================================================================== */