/* ==========================================================================
   Product List - Slider shortcode styles - begin
   ========================================================================== */

.mkd-plvdsl-holder {
	border: 2px solid #b6c0eb;
	.mkd-plvdsl-deals {
		position: absolute;
		background-color: #4861cc;
		top: -40px;
		left: 15px;
		padding: 0 18px;

		h2 {
			color: #fff;
			margin: 7px 0;
		}
	}

	.mkd-owl-slider .owl-nav .owl-next,
	.mkd-owl-slider .owl-nav .owl-prev {
		text-align: center;
		width: 42px;
		height: 42px;
		background: #b6c0eb;
		top: -42px;
		@include mkdTransform(none);

		> span {
			font-size: 23px;
			line-height: 42px;
			color: rgba(255, 255, 255, .5);
		}

		&:hover {
			> span {
				color: #fff;
			}
		}
	}

	.mkd-owl-slider .owl-nav .owl-prev {
		left: 224px;

		&:after {
			content: '';
			position: absolute;
			right: 0;
			width: 1px;
			background: #c5cdef;
			color: rgba(255, 255, 255, .5);
			height: 25px;
			display: block;
			top: 8px;
		}
	}

	.mkd-owl-slider .owl-nav .owl-next {
		left: 266px;
		right: auto;
	}

	.mkd-pvd-left {
		padding: 106px 50px 46px 50px;
		width: 100%;
		position: relative;
		box-sizing: border-box;

		.mkd-pli-inner {
			width: 50%;
		}

		.mkd-pvd-compare,
		.mkd-pvd-add-to-cart {
			position: absolute;
		}

		.mkd-pvd-compare {
			top: 19px;
			right: 12px;

			.compare-button {
				width: inherit;
			}
		}

		.mkd-pvd-add-to-cart {
			bottom: 12px;
			right: 25px;
			text-align: right;

			.add_to_cart_button, .product_type_external {
				position: absolute;
				right: 0;
				bottom: 14px;
				width: 43px;
				height: 43px;
				font-size: 0;
				border-radius: 50%;
				padding: 0;
				background-color: #f9f9f9;
				@include mkdTransition(all .2s ease-in-out);

				&:before {
					content: "\6b";
					font-family: "dripicons-v2" !important;
					position: absolute;
					top: 0;
					left: 47%;
					color: #fff;
					font-size: 9px;
					text-transform: none !important;
					opacity: 0;
					@include mkdTransform(translate(-47%, -100%));
					-webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
					-moz-transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
					transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
				}

				&:after {
					position: absolute;
					top: 50%;
					left: 47%;
					@include mkdTransform(translate(-47%, -50%));
					content: "\51";
					font-family: "dripicons-v2" !important;
					font-size: 18px;
					color: #8e8e8e;
					-webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1);
					-moz-transition: transform .35s cubic-bezier(.53,.01,.18,1);
					transition: transform .35s cubic-bezier(.53,.01,.18,1);
				}
				&.added {
					display: none;
				}

				&:hover {
					background-color: $first-main-color;

					&:before {
						@include mkdTransform(translate(-30%, 0));
						opacity: 1;
						color: #fff;
					}

					&:after {
						color: #fff;
						@include mkdTransform(translate(-50%, -40%));
						-webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1);
						-moz-transition: transform .35s cubic-bezier(.53,.01,.18,1);
						transition: transform .35s cubic-bezier(.53,.01,.18,1);
					}
				}
			}

			.added_to_cart  {
				position: absolute;
				right: 0;
				bottom: 14px;
				width: 43px;
				height: 43px;
				font-size: 0;
				border-radius: 50%;
				padding: 0;
				background-color: #f9f9f9;
				@include mkdTransition(all .2s ease-in-out);



				&:after {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);
					content: "\53";
					font-family: "dripicons-v2" !important;
					font-size: 18px;
					color: #8e8e8e;
				}
				&:hover {
					background-color: $first-main-color;
					&:after {
						color: #fff;
					}
				}
			}
		}

		.mkd-pli-content {
			width: 50%;
			padding: 0px 0 0 55px;
			vertical-align: initial;

			.mkd-pvd-category {
				line-height: 11px;
				margin: 0 0 11px;
			}

			.mkd-pvd-title {
				margin: 0 0 9px;
				padding-right: 20%;
			}


			.mkd-offer {
				margin: 31px 0 24px;
				font-size: 14px;
			}

			.mkd-vd-countdown {
				.countdown-section {
					.countdown-amount {
						background-color: #f2f3f6;
						margin-right: 9px;
						margin-left: 9px;
						min-width: 43px;
						padding: 12px;
						font-size: 22px;
						font-weight: 500;
						color: #000;
					}
				}
			}
		}
	}
}

/* ==========================================================================
   Product List - Slider shortcode styles - end
   ========================================================================== */